import { GRID_CELL } from "app/dom/constants";
import { EditEdge } from "diagram/models/edit/edit";
import { EdgesPoints, getLineWithIntersection } from "./intersect/intersect";
import { EdgePoint, EdgePoints } from "./point";

export interface EdgePath {
    paths: string[];
    length: number;
}

type Point = EdgePoint;

const RADIUS = GRID_CELL / 2;

const getAnchorAxis = (A: Point, B: Point, key: keyof Point) => {
    const [a, b] = [A[key], B[key]];
    const r = Math.min(RADIUS, Math.abs(a - b) / 2);
    if (a === b) return a;
    return b + r * (a > b ? 1 : -1);
};

const getAnchor = (A: Point, B: Point): Point => ({
    x: getAnchorAxis(A, B, "x"),
    y: getAnchorAxis(A, B, "y"),
});

const isDiagonal = (A: Point, B: Point): boolean => B.x !== A.x && B.y !== A.y;

const pushPoint = (
    props: Props,
    points: Point[],
    path: string[],
    i: number
): void => {
    // PrevPoint (A) > CurrentPoint (B) > NextPoint (C)
    const [A, B, C] = [points[i - 1], points[i], points[i + 1]];
    // We only round 90deg corners
    if (isDiagonal(A, B) || isDiagonal(B, C)) {
        if (props.edgesPoints === null) {
            path.push(`L ${B.x} ${B.y}`);
        } else {
            const ps = props.edgesPoints;
            const line = getLineWithIntersection(ps, props.edge.id, A, B);
            path.push(line);
        }
    } else {
        // Round the corner
        const [X, Y] = [getAnchor(A, B), getAnchor(C, B)];
        // @TODO: Combine this with the above logic
        if (props.edgesPoints === null) {
            path.push(`L ${X.x} ${X.y}`);
        } else {
            const ps = props.edgesPoints;
            const line = getLineWithIntersection(ps, props.edge.id, A, X);
            path.push(line);
        }
        path.push(`Q ${B.x} ${B.y} ${Y.x} ${Y.y}`);
        points[i] = Y;
    }
};

const getPathD = (props: Props, orgPoints: EdgePoint[]): string => {
    if (orgPoints.length === 0) return "";
    if (orgPoints.length === 1) throw Error("points.length must be 0 or 2+");
    const points = [...orgPoints];
    const path = [`M ${points[0].x} ${points[0].y}`];
    const last = points.length - 1;
    for (let i = 1; i < last; i++) {
        pushPoint(props, points, path, i);
    }
    path.push(`L ${points[last].x} ${points[last].y}`);
    return path.join(" ");
};

const getPathLength = (points: EdgePoint[]): number =>
    points.reduce((prev, point, index) => {
        if (index === 0) return 0;
        const { x: x1, y: y1 } = point;
        const { x: x2, y: y2 } = points[index - 1];
        return prev + Math.sqrt(Math.pow(x1 - x2, 2) + Math.pow(y1 - y2, 2));
    }, 0);

const getPoints = (map: EdgePoints, index: number): Point[] => {
    const { from, mid: C, to } = map;
    // FromInner > FromOuter > Mid > ToOuter > ToInner
    // A           B           C     D         E
    const [A, E] = [from.inners[index], to.inners[index]];
    const [B, D] = [from.outer, to.outer];
    // Simple case where one row link to one row
    const isSingle = A.x === B.x && D.x === E.x;
    return isSingle ? [A, ...C, E] : [A, B, ...C, D, E];
};

interface Props {
    edge: EditEdge;
    edgePoints: EdgePoints;
    edgesPoints: EdgesPoints | null;
}

export const getEdgePath = (props: Props): EdgePath => {
    const { edge, edgePoints, edgesPoints } = props;
    const { from, mid, to } = edgePoints;

    const paths: string[] = from.inners.map((_, index) => {
        const points = getPoints(edgePoints, index);
        const path = getPathD(props, points);
        edgesPoints?.set(edge.id, points);
        return path;
    });

    const length: number = getPathLength([
        from.inners[0],
        ...mid,
        to.inners[0],
    ]);

    return { paths, length };
};
