import { addPoints, multiplyPoint, subtractPoints } from "./segment";

interface Point {
    x: number;
    y: number;
}

export const getSegmentLength = (A: Point, B: Point): number => {
    return Math.sqrt(Math.pow(A.x - B.x, 2) + Math.pow(A.y - B.y, 2));
};

/**
 * Split "B" into C and D where C and D are on AB line and CB = BD = len.
 * Returns [C, D] array.
 */
export const splitSegment = (
    A: Point,
    B: Point,
    len: number
): [Point, Point] => {
    const dAB = getSegmentLength(A, B);
    // B = A + 1 * r
    const r = subtractPoints(B, A);
    // C = A + t * r where t is AC / CB (%)
    const dCB = len;
    const t = 1 - dCB / dAB;
    const C = addPoints(A, multiplyPoint(r, t));
    // D = A + u * r similar to C
    const u = 1 + dCB / dAB;
    const D = addPoints(A, multiplyPoint(r, u));
    return [C, D];
};
