import { EditRow, EditTable } from "diagram/models/edit/edit";
import * as React from "react";
import { groupTableRows, TableRowsItem } from "./group";
import { RowEdgesMap, RowEdges } from "app/view/canvas/edge";

interface SharedProps {
    table: EditTable;
    getRow: (row: EditRow, rowEdges: RowEdges | undefined) => JSX.Element;
    getHiddenRows: (item: EditRow[]) => JSX.Element;
    getHideRowsButton: () => JSX.Element | null;
}

interface Props extends SharedProps {
    rowEdgesMap: RowEdgesMap | undefined;
}

/** Props of each Item, which could be a row or a group of rows */
interface ItemProps extends SharedProps {
    rowEdgesMap: RowEdgesMap | undefined;
    item: TableRowsItem;
}

const Item = (props: ItemProps) => {
    if (!Array.isArray(props.item)) {
        // Single row
        const row = props.item;
        return props.getRow(row, props.rowEdgesMap?.get(row.id));
    } else {
        // Group of rows
        if (props.table.isRowsExpand === false) {
            return props.getHiddenRows(props.item);
        } else {
            return (
                <React.Fragment>
                    {props.item.map((row) => {
                        const edges = props.rowEdgesMap?.get(row.id);
                        return props.getRow(row, edges);
                    })}
                </React.Fragment>
            );
        }
    }
};

export const TableRowsHeadless = (props: Props) => {
    const { rows } = props.table.value;
    const items = groupTableRows(rows, props.rowEdgesMap);
    return (
        <>
            {items.map((item) => (
                <Item
                    {...props}
                    key={Array.isArray(item) ? item[0].id : item.id}
                    rowEdgesMap={props.rowEdgesMap}
                    item={item}
                />
            ))}
            {props.table.isRowsExpand &&
                items.length !== rows.length &&
                props.getHideRowsButton()}
        </>
    );
};
