/*
The code here comes from https://github.com/pgkelley4/line-segments-intersect
which come from http://stackoverflow.com/a/565282/786339
It is modified to more suitable to DiagramPlus
*/

interface Point {
    x: number;
    y: number;
}

/** Subtract the second point from the first. */
export const subtractPoints = (point1: Point, point2: Point): Point => ({
    x: point1.x - point2.x,
    y: point1.y - point2.y,
});

/** Add the second point to the first. */
export const addPoints = (point1: Point, point2: Point): Point => ({
    x: point1.x + point2.x,
    y: point1.y + point2.y,
});

/** Multiply point by a number */
export const multiplyPoint = (point: Point, i: number): Point => ({
    x: point.x * i,
    y: point.y * i,
});

/** Calculate the cross product of the two points. */
const crossProduct = (point1: Point, point2: Point): number => {
    return point1.x * point2.y - point1.y * point2.x;
};

/**
 * Return the intersection point between 2 line segments, or null otherwise.
 * When the intersection is a segment instead of a line, consider it as null.
 */
export const getSegmentIntersection = (
    p1: Point,
    p2: Point,
    q1: Point,
    q2: Point
): Point | null => {
    const r = subtractPoints(p2, p1);
    const s = subtractPoints(q2, q1);

    const uNumerator = crossProduct(subtractPoints(q1, p1), r);
    const denominator = crossProduct(r, s);

    // Segments are collinear. Technically there can be no overlap, or the
    // intersection will be a segment. However, under DiagramPlus's perspective
    // we don't care about both
    if (uNumerator === 0 && denominator === 0) return null;

    // Segments are paralell.
    if (denominator === 0) return null;

    const u = uNumerator / denominator;
    const t = crossProduct(subtractPoints(q1, p1), s) / denominator;

    // Segments are not parallel but do not intersect.
    if (t < 0 || t > 1 || u < 0 || u > 1) return null;

    const addend = multiplyPoint(r, t);
    const intersection = addPoints(p1, addend);
    return intersection;
};
