import * as Export from "diagram/export/export";
import { Platform } from "utils/platform";
import { saveFile } from "utils/file/file";

interface Props {
    options: Export.ExportImageOptions;
    result: Export.ExportImageResult;
    platform: Platform;
}

export const exportImage = (props: Props): void => {
    saveFile({
        platform: props.platform,
        format: props.options.format,
        content: props.result.body,
        name: "diagram",
    });
};
