import { ButtonMenuItem } from "@moai/core";
import * as Export from "diagram/export/export";
import { EditDiagram } from "diagram/models/edit/edit";
import { showAlert } from "@moai/core";
import { copyToClipboard } from "utils/clipboard";
import { saveFile } from "utils/file/file";
import { Platform } from "utils/platform";

const toText = (diagram: EditDiagram) => {
    type Options = Export.ExportTextOptions;
    const options: Options = { type: "text", level: "edit", format: "json" };
    return Export.exportDiagram(diagram, options).value;
};

const toClipboard = async (diagram: EditDiagram): Promise<void> => {
    const text = toText(diagram);
    await copyToClipboard(text);
    await showAlert([
        "Your diagram is copied to clipboard",
        `To import it, go to "Import" > "Import from text"`,
    ]);
};

const toFile = (platform: Platform, diagram: EditDiagram): void => {
    saveFile({
        platform,
        format: "diagram",
        name: "diagram",
        content: toText(diagram),
    });
};

export const getHeaderExportTextItems = (
    platform: Platform,
    diagram: EditDiagram
): ButtonMenuItem[] => [
    { label: "Save to file", fn: () => toFile(platform, diagram) },
    { label: "Copy to clipboard", fn: () => void toClipboard(diagram) },
];
