import { DivPx } from "@moai/core";
import { SetSelectIds } from "app/view/select/select";
import { LibraryProps } from "diagram/library/state";
import { EditDiagram } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import { UndoAction } from "diagram/state/undo/undo";
import { PrefsProps } from "pref/pref";
import React from "react";
import { Platform, usePlatform } from "utils/platform";
import { HeaderChildProps } from "../header";
import { HeaderBigDiv } from "../utils/div";
import { HeaderAddTable } from "./add";
import { HeaderExport } from "./export/export";
import { HeaderHistory } from "./history";
import { HeaderImport } from "./import/import";
import { HeaderLayout } from "./layout/layout";
import { HeaderLibrary } from "./library/library";
import { HeaderSearch } from "./search";
import { HeaderSetting } from "./setting/setting";
import sBase from "./style/base.module.scss";
import sMac from "./style/mac.module.scss";
import sWin from "./style/win.module.scss";
import { HeaderZoom } from "./zoom/zoom";

export interface HeaderPrimaryProps
    extends HeaderChildProps,
        PrefsProps,
        LibraryProps {
    // Diagram
    diagram: EditDiagram;
    diagramPast: EditDiagram[];
    diagramFuture: EditDiagram[];
    diagramDispatch: React.Dispatch<DiagramAction | UndoAction>;
    // State
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>;
    setSelectIds: SetSelectIds;
    // Header
    isHeaderExpand: boolean;
    setIsHeaderExpand: React.Dispatch<React.SetStateAction<boolean>>;
}

const getContainerClass = (platform: Platform): string => {
    const cls = [sBase.container];
    if (platform === "mac") cls.push(sMac.container);
    if (platform === "win") cls.push(sWin.container);
    return cls.join(" ");
};

export const HeaderPrimary = (props: HeaderPrimaryProps) => {
    const platform = usePlatform();
    return (
        <div className={getContainerClass(platform)}>
            <DivPx size={16} />
            <HeaderLibrary
                prefs={props.prefs}
                setPrefs={props.setPrefs}
                size={props.size}
                style={props.style}
            />
            <DivPx size={16} />
            <HeaderImport
                diagramDispatch={props.diagramDispatch}
                size={props.size}
                style={props.style}
            />
            <HeaderBigDiv />
            <HeaderAddTable
                tables={props.diagram.tables}
                diagramDispatch={props.diagramDispatch}
                setSelectIds={props.setSelectIds}
                size={props.size}
                style={props.style}
            />
            <DivPx size={16} />
            <HeaderHistory
                past={props.diagramPast}
                future={props.diagramFuture}
                dispatch={props.diagramDispatch}
                size={props.size}
                style={props.style}
            />
            <HeaderBigDiv />
            <HeaderSearch
                diagram={props.diagram}
                setSelectIds={props.setSelectIds}
                size={props.size}
                style={props.style}
            />
            <HeaderBigDiv />
            <HeaderLayout
                diagramDispatch={props.diagramDispatch}
                size={props.size}
                style={props.style}
            />
            <DivPx size={16} />
            <HeaderZoom
                prefs={props.prefs}
                setPrefs={props.setPrefs}
                size={props.size}
                style={props.style}
            />
            <DivPx size={16} />
            <HeaderExport
                diagram={props.diagram}
                setIsCapture={props.setIsCapture}
                size={props.size}
                style={props.style}
            />
            <HeaderBigDiv />
            <HeaderSetting
                isHeaderExpand={props.isHeaderExpand}
                setIsHeaderExpand={props.setIsHeaderExpand}
                size={props.size}
                style={props.style}
            />
            <DivPx size={16} />
        </div>
    );
};
