import { Platform } from "utils/platform";
import * as Export from "diagram/export/export";
import { ButtonMenuItem } from "@moai/core";
import { exportSqlDrivers } from "diagram/export/sql/sql";
import { EditDiagram } from "diagram/models/edit/edit";
import { exportSqlToHost, UseHostInstruction } from "utils/host";
import * as React from "react";
import { showAlert } from "@moai/core";

export interface ExportProps {
    options: Export.ExportSqlOptions;
    result: Export.ExportSqlResult;
    platform: Platform;
}

const exportSql = (props: ExportProps): void => {
    if (props.platform === "web")
        throw Error('Calling exportSql but current platform is "web"');
    exportSqlToHost(props.platform, {
        data: props.result.base,
        driver: props.options.driver,
    });
};

interface Props {
    diagram: EditDiagram;
    platform: Platform;
}

const appMessage = [
    "Export to database requires TablePlus app",
    <UseHostInstruction />
];

export const getHeaderExportSqlItems = (props: Props): ButtonMenuItem[] => {
    const { diagram, platform } = props;
    if (platform === "web") {
        return [
            {
                label: "Export to a database…",
                fn: () => void showAlert(appMessage),
            },
        ];
    } else {
        return exportSqlDrivers.map((driver) => ({
            label: `Export to ${driver}`,
            fn: () => {
                type Options = Export.ExportSqlOptions;
                const options: Options = { type: "sql", driver };
                const result = Export.exportDiagram(diagram, options);
                exportSql({ result, options, platform });
            },
        }));
    }
};
