import { EditRow } from "diagram/models/edit/edit";
import { MAX_ROWS_COUNT } from "app/dom/constants";
import { RowEdgesMap } from "app/view/canvas/edge";

export type TableRowsItem = EditRow | EditRow[];

export const groupTableRows = (
    rows: EditRow[],
    orgRowEdgesMap: RowEdgesMap | undefined
): TableRowsItem[] => {
    const rowEdgesMap = orgRowEdgesMap ?? new Map();
    // When slotCount reaches 0 then start grouping rows
    let slotCount = MAX_ROWS_COUNT - rowEdgesMap.size;
    // Prepare
    const items: TableRowsItem[] = [];
    let prevGroup: EditRow[] = [];
    // Push row or group rows
    rows.forEach((row) => {
        if (slotCount > 0 || rowEdgesMap.has(row.id)) {
            if (prevGroup.length !== 0) items.push(prevGroup);
            prevGroup = [];
            items.push(row);
            slotCount = slotCount - 1;
        } else {
            prevGroup.push(row);
        }
    });
    if (prevGroup.length !== 0) items.push(prevGroup);
    // Return
    return items;
};
