import { ButtonMenu, ButtonMenuItem } from "@moai/core";
import { icons } from "@moai/icon";
import * as Export from "diagram/export/export";
import { EditDiagram } from "diagram/models/edit/edit";
import React from "react";
import { Platform, usePlatform } from "utils/platform";
import { HeaderChildProps } from "../../header";
import { exportImage } from "./image";
import { getHeaderExportSqlItems } from "./sql";
import { getHeaderExportTextItems } from "./text";

interface Props extends HeaderChildProps {
    diagram: EditDiagram;
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>;
}

const sleep = async (num: number): Promise<void> => {
    await new Promise((resolve) => window.setTimeout(() => resolve(), num));
};

const getImageItems = (
    diagram: EditDiagram,
    platform: Platform,
    setIsCapture: React.Dispatch<React.SetStateAction<boolean>>
): ButtonMenuItem[] => {
    const images: [string, Export.ExportImageOptions["format"], 1 | 2][] = [
        ["Save as PNG (High quality)", "png", 2],
        ["Save as PNG (Low quality)", "png", 1],
        ["Save as SVG (Web page)", "svg", 1],
    ];
    return images.map(([label, format, scale]) => {
        const fn = async () => {
            setIsCapture(true);
            await sleep(100);
            type Options = Export.ExportImageOptions;
            const options: Options = { type: "image", format, scale };
            const result = await Export.exportDiagram(diagram, options);
            exportImage({ result, options, platform });
            setIsCapture(false);
        };
        return { label, fn };
    });
};

export const HeaderExport = (props: Props) => {
    const { diagram, setIsCapture } = props;
    const platform = usePlatform();
    return (
        <ButtonMenu
            items={[
                ...getHeaderExportTextItems(platform, diagram),
                "divider",
                ...getImageItems(diagram, platform, setIsCapture),
                "divider",
                ...getHeaderExportSqlItems({ diagram, platform }),
            ]}
            button={{
                icon: icons.share,
                size: props.size.button,
                style: props.style.button,
                title: "Export",
            }}
        />
    );
};
