import { Prefs } from "pref/pref";
import * as React from "react";
import { ResizeDiv } from "utils/resize-div/resize-div";
import s from "./resizer.module.css";

interface Props {
    libraryRef: React.RefObject<HTMLDivElement>;
    setPrefs: React.Dispatch<React.SetStateAction<Prefs>>;
}

const setLibraryWidth = (width: number) => (prev: Prefs): Prefs => ({
    ...prev,
    libraryWidth: width,
});

export const ViewResizer = (props: Props) => (
    <div className={s.container}>
        <ResizeDiv
            fallback={320}
            min={240}
            max={800}
            onChange={(width) => {
                const element = props.libraryRef.current;
                if (element === null) throw Error("LibraryRef is null");
                element.style.width = `${width}px`;
            }}
            onStop={(width) => {
                props.setPrefs(setLibraryWidth(width));
            }}
        />
    </div>
);
