import { EditEdge, EditTable } from "diagram/models/edit/edit";

export interface RowEdges {
    from: EditEdge | null;
    to: Set<EditEdge>;
}

export type RowEdgesMap = Map<number /* RowId */, RowEdges>;

export type RelatedTable = [EditEdge, EditTable]

export interface TableEdgesInfo {
    /** Map from table's row's id to related edges */
    rowEdgesMap: RowEdgesMap;
    /** List of edges and the table at the other side of the edge */
    relatedTables: RelatedTable[];
}

export type TableEdgesInfoMap = Map<number /* TableId */, TableEdgesInfo>;

type TableMap = Map<number /* TableId */, EditTable>;

const newInfo = (): TableEdgesInfo => ({
    rowEdgesMap: new Map(),
    relatedTables: [],
});

const updateEdgeFrom = (
    map: TableEdgesInfoMap,
    tableMap: TableMap,
    edge: EditEdge
): void => {
    const info: TableEdgesInfo = map.get(edge.from.table) ?? newInfo();

    // Update rowEdgesMap
    const rowEdgesMap = new Map(info.rowEdgesMap);
    edge.from.rows.forEach((rowId) => {
        const to = rowEdgesMap.get(rowId)?.to ?? new Set();
        rowEdgesMap.set(rowId, { from: edge, to });
    });

    // Update otherTables
    const otherTables = [...info.relatedTables];
    otherTables.push([edge, tableMap.get(edge.to.table)!]);

    map.set(edge.from.table, { rowEdgesMap, relatedTables: otherTables });
};

const updateEdgeTo = (
    map: TableEdgesInfoMap,
    tableMap: TableMap,
    edge: EditEdge
): void => {
    const info: TableEdgesInfo = map.get(edge.to.table) ?? newInfo();

    // Update rowEdgesMap
    const rowEdgesMap = new Map(info.rowEdgesMap);
    edge.to.rows.forEach((rowId) => {
        const from = rowEdgesMap.get(rowId)?.from ?? null;
        const to = new Set(rowEdgesMap.get(rowId)?.to);
        to.add(edge);
        rowEdgesMap.set(rowId, { from, to });
    });

    // Update otherTables
    const otherTables = [...info.relatedTables];
    otherTables.push([edge, tableMap.get(edge.from.table)!]);

    map.set(edge.to.table, { rowEdgesMap, relatedTables: otherTables });
};

export const getTableEdgesInfoMap = (
    tableMap: TableMap,
    edges: EditEdge[]
): TableEdgesInfoMap => {
    const map: TableEdgesInfoMap = new Map();
    edges.forEach((edge) => {
        updateEdgeFrom(map, tableMap, edge);
        updateEdgeTo(map, tableMap, edge);
    });
    return map;
};
