import { Border, DivGrow } from "@moai/core";
import { EditDiagram } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import { LibraryProps } from "diagram/library/state";
import * as React from "react";
import { LibraryCreate } from "./create/create";
import { LibraryItem } from "./item/item";
import s from "./library.module.css";

export interface LibraryPaneProps extends LibraryProps {
    diagram: EditDiagram;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const LibraryPane = (props: LibraryPaneProps) => (
    <div className={s.container}>
        <div className={s.header}>
            <h1 className={s.title}>Diagrams</h1>
            <DivGrow />
            <LibraryCreate {...props} />
        </div>
        <Border color="weak" />
        {props.library.all.map((name) => (
            <React.Fragment key={name}>
                <LibraryItem {...props} name={name} />
                <Border color="weak" />
            </React.Fragment>
        ))}
    </div>
);
