import { Border, Button, DivPx, Input } from "@moai/core";
import { icons } from "@moai/icon";
import { EditTable } from "diagram/models/edit/edit";
import * as React from "react";
import s from "./rows.module.css";

interface Props {
    idKey: string;
    table: EditTable | null;
    rows: string[];
    setRows: React.Dispatch<React.SetStateAction<string[]>>;
}

type Reducer = (rows: string[]) => string[];

const addRow = (): Reducer => {
    return (rows) => rows.concat("");
};

const removeRow = (index: number): Reducer => {
    return (rows) => rows.filter((_r, i) => i !== index);
};

const setRow = (index: number, row: string): Reducer => {
    return (rows) => rows.map((r, i) => (i === index ? row : r));
};

const getList = (props: Props): string[] => {
    if (props.table === null) return [];
    return props.table.value.rows
        .map((row) => row.value[props.idKey] as string)
        .filter((row) => !props.rows.includes(row));
};

const dispatchRemove = (props: Props, index: number) => {
    return (event: React.MouseEvent) => {
        props.setRows(removeRow(index));
        event.stopPropagation();
        // Because "click outside to close popup" is handled outside of React's
        // event system. See https://stackoverflow.com/q/24415631/6621213
        event.nativeEvent.stopImmediatePropagation();
    };
};

export const CreateEdgeRows = (props: Props) => (
    <div>
        {props.rows.map((row, index) => (
            <div className={s.row} key={index}>
                <div className={s.input}>
                    <Input
                        value={row}
                        setValue={(value) => {
                            props.setRows(setRow(index, value));
                        }}
                        // @TODO(https://github.com/dvkndn/moai/issues/9)
                        // Use same list for all inputs
                        list={{
                            id: `${props.table?.id}-rows-${index}`,
                            values: getList(props),
                        }}
                        size={Input.size.small}
                        style={Input.style.flat}
                        placeholder="Column"
                    />
                </div>
                <DivPx size={8} />
                <Border color="weak" />
                <DivPx size={8} />
                {index === 0 ? (
                    <Button
                        onClick={() => void props.setRows(addRow())}
                        icon={icons.plus}
                        style={Button.style.flat}
                        size={Button.size.small}
                        title="Add column (Composite foreign key)"
                    />
                ) : (
                    <Button
                        onClick={dispatchRemove(props, index)}
                        icon={icons.cross}
                        style={Button.style.flat}
                        size={Button.size.small}
                        title="Remove column from reference"
                    />
                )}
            </div>
        ))}
    </div>
);
