import { Button } from "@moai/core";
import { icons } from "@moai/icon";
import * as React from "react";
import { HeaderChildProps } from "../../header";

interface Props extends HeaderChildProps {
    isHeaderExpand: boolean;
    setIsHeaderExpand: React.Dispatch<React.SetStateAction<boolean>>;
}

export const HeaderSetting = (props: Props) => (
    <Button
        selected={props.isHeaderExpand}
        onClick={() => props.setIsHeaderExpand((bool) => !bool)}
        icon={icons.cog}
        size={props.size.button}
        style={props.style.button}
        title="Settings"
    />
);
