import { background, Border, borderColor, text } from "@moai/core";
import { TABLE_CLIENT_WIDTH } from "app/dom/constants";
import { getRowElementId } from "app/dom/dom";
import { getRowHeight } from "app/dom/size";
import { RowEdges, RowEdgesMap } from "app/view/canvas/edge";
import { EditColumn, EditRow, EditTable } from "diagram/models/edit/edit";
import * as React from "react";
import { TableRowsHeadless } from "../rows/headless";

interface SharedProps {
    table: EditTable;
    columns: EditColumn[];
}

interface Props extends SharedProps {
    rowEdgesMap: RowEdgesMap | undefined;
}

interface RowProps extends SharedProps {
    row: EditRow;
    rowEdges: RowEdges | undefined;
}

const Field = (props: RowProps & { index: number }) => (
    <div
        style={{
            flex: props.index === 0 ? "1 1 0px" : "0 0 auto",
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
            textAlign: props.index === 1 ? "right" : undefined,
        }}
        className={props.index === 1 ? text.muted : ""}
    >
        {props.row.value[props.columns[props.index].value.name] ?? "<empty>"}
    </div>
);

const getRowStyle = (props: RowProps): React.CSSProperties => {
    const height = getRowHeight(props.rowEdges?.to.size ?? 0);
    return { display: "flex", alignItems: "center", padding: "0 16px", height };
};

const Row = (props: RowProps) => (
    <div
        style={getRowStyle(props)}
        id={getRowElementId(props.table.id, props.row.id)}
    >
        <Field {...props} index={0} />
        <div style={{ width: 8 }} />
        <Field {...props} index={1} />
    </div>
);

const containerProps: React.HTMLAttributes<HTMLDivElement> = {
    className: [background.primary, borderColor.strong].join(" "),
    style: {
        lineHeight: "24px",
        borderStyle: "solid",
        borderWidth: 1,
        width: TABLE_CLIENT_WIDTH + 2,
        margin: -1, // Same for active table
    },
};

const headerStyle: React.CSSProperties = {
    padding: 8,
    fontWeight: 600,
    textAlign: "center",
};

const HiddenRows = (props: { count: number }) => (
    <div style={{ textAlign: "center" }}>{props.count} columns hidden</div>
);

export const TableInactive = (props: Props) => (
    <div {...containerProps}>
        <div style={headerStyle}>{props.table.value.name}</div>
        <Border color="weak" />
        <div style={{ marginTop: -1 }} />
        <div style={{ padding: "8px 0" }}>
            <TableRowsHeadless
                table={props.table}
                rowEdgesMap={props.rowEdgesMap}
                getRow={(row, rowEdges) => (
                    <Row {...props} row={row} rowEdges={rowEdges} />
                )}
                getHiddenRows={(item) => <HiddenRows count={item.length} />}
                getHideRowsButton={() => null}
            />
        </div>
    </div>
);
