import { GRID_CELL } from "app/dom/constants";
/**
 * This module makes an edge "jumps" over others on intersection. In the figure
 * below, edge CD intersects edge AB at X. If CD is drawn later than AB then a
 * "jump" happen at X on CD:
 *
 * A >----+
 *        |
 * C >----X-----> D
 *        |
 *        +-----> B
 */

import { EditEdge } from "diagram/models/edit/edit";
import * as React from "react";
import { EdgePoint } from "../point";
import { getArc } from "./arc";
import { getSegmentIntersection } from "./segment";
import { getSegmentLength, splitSegment } from "./split";

export type EdgesPoints = Map<EditEdge["id"], EdgePoint[]>;

type Ref = React.MutableRefObject<EdgesPoints>;

export const useEdgesPoints = (edges: EditEdge[]): Ref => {
    const ref: Ref = React.useRef<EdgesPoints>(new Map());

    // Remove not existed edges
    React.useEffect(() => {
        const edgesSet = new Set(edges.map((e) => e.id));
        ref.current.forEach((_value, key) => {
            if (edgesSet.has(key) === false) ref.current.delete(key);
        });
    }, [edges]);

    return ref;
};

const INTERSECTION_RADIUS = GRID_CELL / 4;

/**
 * Draw a line segment from A to B. If the segment intersects with other
 * segments in EdgesPoints then a jump (arc) will be drawn at the intersection.
 *
 * Assume that the pointer is already at A
 */
export const getLineWithIntersection = (
    edgesPoints: EdgesPoints,
    edgeId: EditEdge["id"],
    A: EdgePoint,
    B: EdgePoint
): string => {
    let P: EdgePoint = A; // Previous point
    const edgesIds = [...edgesPoints.keys()];
    const path: string[] = [];
    for (let i = 0; i < edgesIds.length; i++) {
        const id = edgesIds[i];
        if (id === edgeId) {
            // We stop here, as a line should not avoid lines added after it
            break;
        };
        const points = edgesPoints.get(id)!;
        for (let j = 0; j < points.length - 1; j++) {
            // CD is current segment to be checked, E is the intersection
            const [C, D] = [points[j], points[j + 1]];
            const E = getSegmentIntersection(P, B, C, D);
            if (E === null) continue;
            // If intersection is too close, we also don't draw the arc
            if (getSegmentLength(P, E) < INTERSECTION_RADIUS) continue;
            if (getSegmentLength(B, E) < INTERSECTION_RADIUS) continue;
            // Split Q to M and N to draw arc
            const [M, N] = splitSegment(P, E, INTERSECTION_RADIUS);
            path.push(`L ${M.x} ${M.y}`);
            path.push(getArc(M, N));
            // N is the new Previous point
            P = N;
        }
    }
    path.push(`L ${B.x} ${B.y}`);
    return path.join(" ");
};
