import { Border, DivPx, Input, MutedSpan } from "@moai/core";
import { EditTable } from "diagram/models/edit/edit";
import * as React from "react";
import { CreateEdgeRows } from "../rows/rows";
import s from "./side.module.css";

interface Props {
    label: string;
    idKey: string;
    tables: EditTable[];
    table: string;
    setTable: React.Dispatch<React.SetStateAction<string>>;
    rows: string[];
    setRows: React.Dispatch<React.SetStateAction<string[]>>;
    autoFocus: boolean;
}

const getList = (props: Props): string[] => {
    return props.tables.map((table) => table.value.name);
};

const getTable = (props: Props): EditTable | null => {
    const byName = (table: EditTable) => {
        return table.value.name === props.table;
    };
    return props.tables.find(byName) ?? null;
};

export const CreateEdgeSide = (props: Props) => (
    <div className={s.container}>
        <div className={s.label}>
            <MutedSpan>{props.label}</MutedSpan>
        </div>
        <DivPx size={8} />
        <div className={s.table}>
            <Input
                value={props.table}
                setValue={props.setTable}
                autoFocus={props.autoFocus}
                size={Input.size.small}
                style={Input.style.flat}
                placeholder="Table"
                list={{
                    id: `${props.label}-table`,
                    values: getList(props),
                }}
            />
        </div>
        <DivPx size={8} />
        <Border color="weak" />
        <DivPx size={8} />
        <CreateEdgeRows
            idKey={props.idKey}
            table={getTable(props)}
            rows={props.rows}
            setRows={props.setRows}
        />
    </div>
);
