import { Checkbox } from "@moai/core";
import { PrefsProps } from "pref/pref";
import * as React from "react";

interface Props extends PrefsProps {}

export const HeaderFooter = (props: Props) => (
    <Checkbox
        value={props.prefs.footerVisible}
        setValue={(value) =>
            props.setPrefs((prev) => ({ ...prev, footerVisible: value }))
        }
        children="Show footer"
    />
);
