import { EditDiagram } from "diagram/models/edit/edit";
import { makeBaseDiagramFromEdit } from "../base";
import { ExportTextOptions, ExportTextResult } from "../export";
import { encodeText } from "utils/encode";

export const exportDiagramAsText = (
    diagram: EditDiagram,
    options: ExportTextOptions
): ExportTextResult => {
    const obj: object = (() => {
        switch (options.level) {
            case "base":
                return makeBaseDiagramFromEdit(diagram);
            case "edit":
                return diagram;
        }
    })();
    const json: string = JSON.stringify(obj);
    const value: string = (() => {
        switch (options.format) {
            case "json":
                return json;
            case "base64":
                return encodeText(json);
        }
    })();
    return { value };
};
