import { DivPx, text } from "@moai/core";
import * as Constants from "app/dom/constants";
import { EditColumn, EditRef, EditRow } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import React from "react";
import { debugLog } from "utils/debug";
import { useRowDropTarget } from "./drag/drop/drop";
import { RowDragSource } from "./drag/source/source";
import { RowFieldInput } from "./field/field";
import { RowRemoveButton, rowRemoveOnKeyDown } from "./remove/remove";
import s from "./row.module.scss";
import { getRowHeight } from "app/dom/size";
import { ShowCreateEdgePopup } from "app/edge/create/state";

interface Props {
    // row
    row: EditRow;
    edgeFromHere: EditRef | null;
    edgesToHereCount: number;
    // table
    tableId: number;
    isTableEdit: boolean;
    isTableColumnsExpand: boolean;
    // diagram
    columns: EditColumn[];
    diagramDispatch: React.Dispatch<DiagramAction>;
    showCreateEdge: ShowCreateEdgePopup;
}

type FieldType = "first" | "second" | "rest";

const renderField = (props: Props, type: FieldType) => (column: EditColumn) => {
    const fieldCls = (() => {
        if (type === "rest") return s.field;
        if (type === "first") return s.field1st;
        return [s.field2nd, text.muted].join(" ");
    })();
    const width = type === "rest" ? Constants.TABLE_BODY_WIDTH / 2 : undefined;
    return (
        <div
            className={[Constants.TABLE_COLUMN_CLASS, fieldCls].join(" ")}
            style={{ width }}
            key={column.id}
        >
            <RowFieldInput
                tableId={props.tableId}
                row={props.row}
                column={column}
                diagramDispatch={props.diagramDispatch}
            />
        </div>
    );
};

const RowPure = (props: Props) => {
    debugLog("Table", props.tableId, "Row", props.row.id, props.row.value.id);

    const [isOver, dropRef] = useRowDropTarget({
        tableId: props.tableId,
        rowId: props.row.id,
    });

    return (
        <div
            className={`${s.container} ${isOver ? s.over : ""}`}
            ref={dropRef}
            style={{ height: getRowHeight(props.edgesToHereCount) }}
            onKeyDown={(event) => {
                rowRemoveOnKeyDown({
                    tableId: props.tableId,
                    rowId: props.row.id,
                    diagramDispatch: props.diagramDispatch,
                })(event);
            }}
        >
            <DivPx size={8} />

            {/* REMOVE */}
            {props.isTableEdit && (
                <div className={Constants.TABLE_COLUMN_CLASS}>
                    <RowRemoveButton
                        tableId={props.tableId}
                        rowId={props.row.id}
                        diagramDispatch={props.diagramDispatch}
                    />
                </div>
            )}

            {/* The first 2 fields are special, their width are flexible but
            the total must be fixed */}
            <div
                className={s.fields}
                style={{ width: Constants.TABLE_BODY_WIDTH }}
            >
                {renderField(props, "first")(props.columns[0])}
                {renderField(props, "second")(props.columns[1])}
            </div>

            {/* The rest are rendered with fixed width of their own */}
            {props.isTableColumnsExpand && (
                <div className={s.fields}>
                    {props.columns.slice(2).map(renderField(props, "rest"))}
                </div>
            )}

            {/* DRAG */}
            {props.isTableEdit && (
                <RowDragSource
                    tableId={props.tableId}
                    rowId={props.row.id}
                    diagramDispatch={props.diagramDispatch}
                    edge={props.edgeFromHere}
                    showCreateEdge={props.showCreateEdge}
                />
            )}

            <DivPx size={8} />
        </div>
    );
};

export const Row = React.memo(RowPure);
