import { getEdgeState } from "app/edge/state/state";
import { getEdgeSubElement } from "app/edge/utils";
import { RelatedTable } from "app/view/canvas/edge";
import { EditTable } from "diagram/models/edit/edit";
import { Position } from "diagram/models/layout";
import { DraggableEventHandler } from "react-draggable";

interface Props {
    table: EditTable;
    relatedTables: RelatedTable[];
}

const updateEdgeElements = (table: EditTable) => (related: RelatedTable) => {
    const [edge, other] = related;
    const isSame = edge.from.table === edge.to.table;
    const isTo = edge.to.table === table.id;
    const path = getEdgeState({
        edge,
        fromTable: isSame ? table : isTo ? other : table,
        toTable: isSame ? table : isTo ? table : other,
        rowYMap: null,
        edgesMids: null,
        edgesPoints: null,
    });
    // Path
    path.paths.forEach((d, index) => {
        getEdgeSubElement(edge.id, "path-main", index).setAttribute("d", d);
    });
    // Highlight
    try {
        const highlight = getEdgeSubElement(edge.id, "highlight");
        highlight.setAttribute("path", "");
    } catch (e) {
        // Nothing, it's ok to fail when highlight is not rendered
    }
};

export const onTableDragMove = (props: Props): DraggableEventHandler => {
    return (_event, data) => {
        // Some browsers always fire the "move" event on mouse down even though
        // the table is not moved yet. This checks is to prevent entering
        // "optimized" mode unnecessarily. See #16 and #19
        if (data.x === 0 && data.y === 0) return;
        const position: Position = {
            left: props.table.position.left + data.x,
            top: props.table.position.top + data.y,
        };
        const table = { ...props.table, position };
        props.relatedTables.forEach(updateEdgeElements(table));
    };
};
