import { EditColumn, EditTable } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import React from "react";
import s from "./header.module.scss";
import { HeaderName } from "./name/name";
import { TableHeaderMenu } from "./menu/menu";

interface Props {
    table: EditTable;
    columns: EditColumn[];
    isEdit: boolean;
    setIsEdit: React.Dispatch<React.SetStateAction<boolean>>;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const TableHeader: React.FC<Props> = (props) => (
    <div className={`${s.container} ${props.isEdit ? s.edit : ""}`}>
        <div className={s.main}>
            <HeaderName
                table={props.table}
                setIsEdit={props.setIsEdit}
                diagramDispatch={props.diagramDispatch}
            />
            <div className={s.menu}>
                <TableHeaderMenu
                    table={props.table}
                    columns={props.columns}
                    diagramDispatch={props.diagramDispatch}
                />
            </div>
        </div>
    </div>
);
