import { Border, Button, DivPx, TextArea } from "@moai/core";
import * as React from "react";
import { globalLogger } from "utils/external";
import { usePlatform } from "utils/platform";
import { ReportItem, ReportItemState, useReportItem } from "../item/item";
import s from "./form.module.css";
import { APP_VERSION } from "utils/version";
import {
    CURRENT_DIAGRAM_KEY,
    DEFAULT_DIAGRAM_NAME,
} from "diagram/library/state";
import { showAlert } from "@moai/core";

export type ReportType = "uncaught" | "manual";

export interface ReportFormProps {
    type: ReportType;
    title: string;
    description: React.ReactNode;
    footer?: React.ReactNode;
    error?: any;
    onDone?: () => void;
}

const getDiagram = (): string => {
    const name = window.localStorage.getItem(CURRENT_DIAGRAM_KEY);
    const key = `dg/doc/${name ?? DEFAULT_DIAGRAM_NAME}`;
    return window.localStorage.getItem(key) ?? "Not found";
};

interface ReportBody {
    title: string;
    platform: string;
    version: string;
    error?: string;
    message?: string;
    contact?: string;
    agent?: string;
    diagram?: string;
    log?: string;
}

const getItem = (state: ReportItemState): string | undefined =>
    state.has ? state.text : undefined;

const getError = (error: any): string => {
    if (!error) return "";
    return `${error}\n${error.stack}`;
};

export const ReportForm = (props: ReportFormProps) => {
    const isUncaught = props.type === "uncaught";

    const message = useReportItem(() => "");
    const contact = useReportItem(() => "");
    const error = useReportItem(() => getError(props.error));
    const log = useReportItem(() => JSON.stringify(globalLogger));
    const diagram = useReportItem(() => getDiagram());
    const agent = useReportItem(() => window.navigator.userAgent);
    const platform = usePlatform();
    const [isBusy, setIsBusy] = React.useState(false);

    const submitReport = async (event: React.FormEvent) => {
        event.preventDefault();
        setIsBusy(true);
        const body: ReportBody = {
            title: isUncaught ? "Uncaught error" : "Manual report",
            platform,
            version: APP_VERSION,
            error: getItem(error),
            message: getItem(message),
            contact: getItem(contact),
            agent: getItem(agent),
            diagram: getItem(diagram),
            log: getItem(log),
        };
        await window.fetch("https://diagramplus.com/api/report", {
            method: "POST",
            body: JSON.stringify(body),
            headers: { "Content-type": "application/json" },
        });
        showAlert("The report has been sent successfully");
        setIsBusy(false);
        props.onDone?.();
    };

    return (
        <form onSubmit={(event) => submitReport(event)}>
            <h1 className={s.title}>{props.title}</h1>
            <DivPx size={16} />
            <Border color="weak" />
            <DivPx size={16} />
            {props.description}
            <TextArea
                autoFocus
                rows={3}
                value={message.text}
                setValue={message.setText}
            />
            <DivPx size={16} />
            {isUncaught && (
                <>
                    <ReportItem label="Include error detail" state={error} />
                    <DivPx size={8} />
                </>
            )}
            <ReportItem label="Include the diagram" state={diagram} />
            <DivPx size={8} />
            <ReportItem label="Include user agent" state={agent} />
            <DivPx size={8} />
            <ReportItem label="Include application log" state={log} />
            <DivPx size={8} />
            <ReportItem
                state={contact}
                label="Allow us to contact you at:"
                placeholder="name@example.com"
            />
            <DivPx size={16} />
            <Border color="weak" />
            <DivPx size={16} />
            <div className={s.footer}>
                {props.footer}
                <DivPx size={16} />
                <Button
                    type="submit"
                    highlight
                    children="Send Report"
                    isBusy={isBusy}
                />
            </div>
        </form>
    );
};
