import { exportDiagram, ExportTextOptions } from "diagram/export/export";
import { getLibraryDiagram } from "diagram/library/actions";
import { showAlert } from "@moai/core";
import { copyToClipboard } from "utils/clipboard";
import { saveFile } from "utils/file/file";
import { Platform } from "utils/platform";

const exportOptions: ExportTextOptions = {
    type: "text",
    format: "json",
    level: "edit",
};

export const libraryItemExport = async (
    name: string,
    platform: Platform,
    target: "file" | "clipboard"
): Promise<void> => {
    const diagram = getLibraryDiagram(name);
    if (diagram === null) throw Error(`Diagram "${name}" is null`);
    const text = exportDiagram(diagram, exportOptions).value;
    switch (target) {
        case "clipboard":
            await copyToClipboard(text);
            showAlert([
                `Diagram "${name}" is copied to clipboard. `,
                `To import it, go to "Import" > "Import from text"`,
            ]);
            break;
        case "file":
            saveFile({
                platform: platform,
                format: "diagram",
                name: `${name} diagram`,
                content: text,
            });
            break;
    }
};
