import { ButtonMenu, ButtonMenuItem } from "@moai/core";
import { icons } from "@moai/icon";
import { DiagramAction } from "diagram/reducer/reducer";
import * as React from "react";
import { Platform, usePlatform } from "utils/platform";
import { HeaderChildProps } from "../../header";
import { getHeaderImportSampleItems } from "./sample";
import { getHeaderImportSqlItems } from "./sql";
import { getHeaderImportTextItems } from "./text";

interface InternalProps {
    diagramDispatch: React.Dispatch<DiagramAction>;
    platform: Platform;
    setIsBusy: React.Dispatch<React.SetStateAction<boolean>>;
}

const getItems = (props: InternalProps): ButtonMenuItem[] => {
    const items: ButtonMenuItem[] = [
        ...getHeaderImportTextItems(props),
        "divider",
        ...getHeaderImportSampleItems(props),
    ];
    const sql = getHeaderImportSqlItems(props);
    if (sql.length > 0) items.push("divider", ...sql);
    return items;
};

interface Props extends HeaderChildProps {
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const HeaderImport = (props: Props) => {
    const [isBusy, setIsBusy] = React.useState(false);
    const platform = usePlatform();
    const { diagramDispatch } = props;
    return (
        <ButtonMenu
            button={{
                size: props.size.button,
                style: props.style.button,
                icon: icons.import,
                children: "Import",
                isBusy,
                title: "Import a diagram",
            }}
            items={getItems({ setIsBusy, platform, diagramDispatch })}
        />
    );
};
