import { ShowCreateEdgePopup } from "app/edge/create/state";
import { TableEdgesInfo } from "app/view/canvas/edge";
import { SetSelectIds } from "app/view/select/select";
import { EditColumn, EditTable } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import React from "react";
import Draggable from "react-draggable";
import { debugLog } from "utils/debug";
import s from "./active.module.css";
import { onTableDragMove } from "./drag/move";
import { onTableDragStop } from "./drag/stop";
import { TableMain } from "./main/main";

interface Props {
    table: EditTable;
    tableEdgesInfo: TableEdgesInfo | undefined;
    // Diagram
    columns: EditColumn[];
    diagramDispatch: React.Dispatch<DiagramAction>;
    showCreateEdge: ShowCreateEdgePopup;
    // Select
    isSelected: boolean;
    setSelectId: SetSelectIds;
    // View
    zoom: number;
}

export const TableActive = (props: Props) => {
    debugLog("CanvasTable");
    const dragRef = React.useRef<HTMLDivElement>(null);
    const { table, diagramDispatch } = props;
    const relatedTables = props.tableEdgesInfo?.relatedTables ?? [];
    return (
        <Draggable
            position={{ x: 0, y: 0 }}
            onDrag={onTableDragMove({ table, relatedTables })}
            onStop={onTableDragStop({ table, diagramDispatch })}
            nodeRef={dragRef}
            scale={props.zoom}
            cancel="input, button, select, .react-draggable-cancel"
        >
            <div ref={dragRef} className={s.container}>
                <TableMain
                    table={props.table}
                    tableEdgesInfo={props.tableEdgesInfo}
                    // Select
                    isSelected={props.isSelected}
                    setSelectIds={props.setSelectId}
                    // Diagram
                    columns={props.columns}
                    diagramDispatch={props.diagramDispatch}
                    showCreateEdge={props.showCreateEdge}
                />
            </div>
        </Draggable>
    );
};
