import * as React from "react";
import { focusChild } from "app/dom/dom";
import { getAdjacentColumn, getJumpColumn } from "app/dom/navigate";

const getNextColumn = (element: Element, key: string): Element | null => {
    switch (key) {
        case "ArrowRight":
            return getAdjacentColumn(element, "right");
        case "ArrowLeft":
            return getAdjacentColumn(element, "left");
        case "ArrowUp":
            return getJumpColumn(element, "up");
        case "ArrowDown": {
            return getJumpColumn(element, "down");
        }
    }
    return null;
};

export const navigateOnArrowKeys = (event: React.KeyboardEvent): void => {
    switch (event.key) {
        case "ArrowUp":
        case "ArrowRight":
        case "ArrowDown":
        case "ArrowLeft":
            const { target } = event;
            if (!(target instanceof HTMLElement)) return;
            if (target instanceof HTMLInputElement) return;
            // Prevent parent (viewport)'s scrolling
            event.preventDefault();
            const next = getNextColumn(target, event.key);
            if (next === null) return;
            focusChild(next);
    }
};
