import { Button } from "@moai/core";
import { createLibraryDiagram } from "diagram/library/actions";
import { emptyDiagram } from "diagram/library/fallback";
import * as React from "react";
import { LibraryPaneProps } from "../library";
import { ImportPayload } from "diagram/reducer/import/import";
import { icons } from "@moai/icon";

interface Props extends LibraryPaneProps {}

export const LibraryCreate = (props: Props) => (
    <Button
        onClick={() => {
            createLibraryDiagram(emptyDiagram, props.setLibrary);
            const source = emptyDiagram;
            const payload: ImportPayload = { isAppend: false, source };
            props.diagramDispatch({ type: "import", payload });
        }}
        children="Diagram"
        icon={icons.plus}
    />
);
