import { fileExtensions, FileFormat, SaveFileProps } from "./file";

const mimes: {
    [key in FileFormat]: string;
} = {
    png: "image/png;base64",
    svg: "image/svg+xml;charset=utf-8",
    diagram: "text/plain",
};

export const saveFileInWeb = (props: SaveFileProps): void => {
    const link = document.createElement("a");
    link.download = `${props.name}.${fileExtensions[props.format]}`;
    link.href = `data:${mimes[props.format]},${props.content}`;
    link.click();
};
