import { DivGrow, DivPx } from "@moai/core";
import { PrefsProps } from "pref/pref";
import * as React from "react";
import { Platform, usePlatform } from "utils/platform";
import { HeaderChildProps } from "../header";
import { HeaderBigDiv } from "../utils/div";
import { HeaderAnimation } from "./animation/animation";
import { HeaderFooter } from "./footer/footer";
import { HeaderReportButton } from "./report/report";
import sBase from "./style/base.module.scss";
import sMac from "./style/mac.module.scss";
import sWin from "./style/win.module.scss";
import { HeaderTheme } from "./theme/theme";

export interface HeaderSecondaryProps extends HeaderChildProps, PrefsProps {
    setReportVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

const getContainerClass = (platform: Platform): string => {
    const cls = [sBase.container];
    if (platform === "mac") cls.push(sMac.container);
    if (platform === "win") cls.push(sWin.container);
    return cls.join(" ");
};

export const HeaderSecondary = (props: HeaderSecondaryProps) => {
    const platform = usePlatform();
    return (
        <div className={getContainerClass(platform)}>
            <DivPx size={16} />
            {platform === "web" && (
                <>
                    <HeaderTheme {...props} />
                    <HeaderBigDiv />
                </>
            )}
            <HeaderAnimation {...props} />
            <DivPx size={16} />
            {platform === "web" && (
                <>
                    <HeaderFooter {...props} />
                </>
            )}
            <DivGrow />
            <HeaderReportButton
                setReportVisible={props.setReportVisible}
                size={props.size}
                style={props.style}
            />
            <DivPx size={16} />
        </div>
    );
};
