import { background } from "@moai/core";
import { EdgesCanvas } from "app/edge/canvas/canvas";
import { ShowCreateEdgePopup } from "app/edge/create/state";
import { Table } from "app/table/table";
import { EditDiagram, EditTable } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import { Prefs } from "pref/pref";
import React from "react";
import { debugLog } from "utils/debug";
import { Platform, usePlatform } from "utils/platform";
import { SelectDragLayer } from "../select/canvas/drag/drag";
import { SetSelectIds } from "../select/select";
import s from "./canvas.module.scss";
import { getTableEdgesInfoMap } from "./edge";

interface Props {
    // Diagram
    diagram: EditDiagram;
    diagramDispatch: React.Dispatch<DiagramAction>;
    viewportRef: React.RefObject<HTMLDivElement>;
    isCapture: boolean;
    showCreateEdge: ShowCreateEdgePopup;
    // Select
    selectedTables: Set<number>;
    selectedRefs: Set<number>;
    setSelectIds: SetSelectIds;
    // Prefs
    prefs: Prefs;
}

const getContainerStyle = (
    props: Props,
    platform: Platform
): React.CSSProperties => {
    const [{ zoom }, { width, height }] = [props.prefs, props.diagram.size];
    const halfMargin = (zoom - 1) / 2;
    const margin = `${height * halfMargin}px ${width * halfMargin}px`;
    const transform = zoom !== 1 ? `scale(${zoom})` : "";
    const fontSize = platform === "mac" ? 12 : undefined;
    return { width, height, transform, margin, fontSize };
};

export type TablesMap = Map<number, EditTable>;

const toTablesMap = (map: TablesMap, table: EditTable): TablesMap => {
    map.set(table.id, table);
    return map;
};

export const Canvas = (props: Props) => {
    debugLog("Canvas");

    const { refs, tables } = props.diagram;
    const tableMap = React.useMemo(() => {
        return tables.reduce(toTablesMap, new Map());
    }, [tables]);
    const tableEdgesInfoMap = React.useMemo(() => {
        return getTableEdgesInfoMap(tableMap, refs);
    }, [refs, tableMap]);
    const platform = usePlatform();

    return (
        <div
            id="canvas"
            className={[s.container, background.secondary].join(" ")}
            style={getContainerStyle(props, platform)}
        >
            {/* Separate grid so both grid and background color is included
            in exported image */}
            <div className={s.grid} />
            {!props.isCapture && (
                <SelectDragLayer
                    diagram={props.diagram}
                    setSelectIds={props.setSelectIds}
                />
            )}
            <EdgesCanvas
                diagram={props.diagram}
                diagramDispatch={props.diagramDispatch}
                setSelectIds={props.setSelectIds}
                selectedRefs={props.selectedRefs}
                tablesMap={tableMap}
                edgeAnimation={props.prefs.edgeAnimation}
            />
            {props.diagram.tables.map((table) => (
                <Table
                    key={table.id}
                    // Table
                    table={table}
                    tableEdgesInfo={tableEdgesInfoMap.get(table.id)}
                    // Select
                    isSelected={props.selectedTables.has(table.id)}
                    setSelectId={props.setSelectIds}
                    // Diagram
                    columns={props.diagram.columns}
                    diagramDispatch={props.diagramDispatch}
                    showCreateEdge={props.showCreateEdge}
                    // View
                    viewportRef={props.viewportRef}
                    zoom={props.prefs.zoom}
                    isCapture={props.isCapture}
                />
            ))}
        </div>
    );
};
