import React from "react";
import { EditDiagram } from "../models/edit/edit";
import { DiagramAction, diagramReducer } from "../reducer/reducer";
import { useDiagramExternal } from "./external/external";
import { useDiagramUndoShortcut } from "./undo/shortcut";
import * as Undo from "./undo/undo";

export interface DiagramState {
    history: Undo.UndoState<EditDiagram>;
    dispatch: React.Dispatch<DiagramAction | Undo.UndoAction>;
}

const undoReducer = Undo.createUndoReducer(diagramReducer);

export const useDiagram = (initial: EditDiagram): DiagramState => {
    const [history, dispatch] = React.useReducer(undoReducer, undefined, () => {
        return Undo.createUndoInitial(initial);
    });
    const { past, present, future } = history;
    useDiagramExternal(present, dispatch);
    useDiagramUndoShortcut({ past, future, dispatch });
    return { history, dispatch };
};
