import { BaseDiagram } from "diagram/models/base/base";
import * as React from "react";

type Platform = "mac" | "win";

const post = (platform: Platform, method: string, data: any): void => {
    switch (platform) {
        case "mac":
            const handlers = (window as any).webkit.messageHandlers;
            handlers[method].postMessage(data);
            return;
        case "win":
            const message = { message: method, args: data };
            (window as any).CefSharp.PostMessage(message);
            return;
    }
};

interface SqlData {
    data: BaseDiagram;
    driver: string;
}

export const exportSqlToHost = (platform: Platform, data: SqlData): void => {
    post(platform, "exportSQL", data);
};

interface FileData {
    data: string;
    ext: string;
    name: string;
    title: string;
}

export const saveFileToHost = (platform: Platform, data: FileData): void => {
    post(platform, "saveFile", data);
};

const LINK = "https://github.com/TablePlus/diagram-plugin/";

export const UseHostInstruction = () => (
    <span>
        <span>Please install the </span>
        <a href={LINK} children="Diagram plugin" />
        <span> and use it inside TablePlus app</span>
    </span>
);
