import { TABLE_ROW_CLASS } from "app/dom/constants";
import { getRowElementId } from "app/dom/dom";
import { ShowCreateEdgePopup } from "app/edge/create/state";
import { Row } from "app/row/row";
import { TableRowsHeadless } from "app/table/rows/headless";
import { RowEdges, RowEdgesMap } from "app/view/canvas/edge";
import { EditColumn, EditRow, EditTable } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import * as React from "react";
import { TableHiddenRows, TableHideRowsButton } from "./hidden";

/** Shared props between a single row and a list of rows */
interface SharedProps {
    // Table
    table: EditTable;
    rowEdgesMap: RowEdgesMap | undefined;
    isEdit: boolean;
    isColumnsExpand: boolean;
    // Diagram
    columns: EditColumn[];
    diagramDispatch: React.Dispatch<DiagramAction>;
    showCreateEdge: ShowCreateEdgePopup;
}

/** Props of this component */
interface Props extends SharedProps {}

/** Props of each RowContainer */
interface RowContainerProps extends SharedProps {
    rowEdges: RowEdges | undefined;
    row: EditRow;
}

const RowContainer = (props: RowContainerProps) => (
    <div
        className={TABLE_ROW_CLASS} // For keyboard navigation
        id={getRowElementId(props.table.id, props.row.id)}
    >
        <Row
            // row
            row={props.row}
            edgeFromHere={props.rowEdges?.from ?? null}
            edgesToHereCount={props.rowEdges?.to.size ?? 0}
            // table
            tableId={props.table.id}
            isTableEdit={props.isEdit}
            isTableColumnsExpand={props.isColumnsExpand}
            // diagram
            columns={props.columns}
            diagramDispatch={props.diagramDispatch}
            showCreateEdge={props.showCreateEdge}
        />
    </div>
);

export const TableBodyRows = (props: Props) => (
    <TableRowsHeadless
        // Data
        table={props.table}
        rowEdgesMap={props.rowEdgesMap}
        // Components
        getHideRowsButton={() => (
            <TableHideRowsButton
                tableId={props.table.id}
                diagramDispatch={props.diagramDispatch}
            />
        )}
        getHiddenRows={(item) => (
            <TableHiddenRows
                count={item.length}
                tableId={props.table.id}
                diagramDispatch={props.diagramDispatch}
            />
        )}
        getRow={(row, rowEdges) => (
            <RowContainer {...props} row={row} rowEdges={rowEdges} />
        )}
    />
);
