import { TABLE_OFFSET_WIDTH } from "app/dom/constants";
import * as Edit from "diagram/models/edit/edit";
import { getNumberId, getStringId } from "../id";
import { DiagramReducer } from "../reducer";

export interface DuplicateTablePayload {
    id: number;
}

type Payload = DuplicateTablePayload;

export const duplicateTable: DiagramReducer<Payload> = (diagram, payload) => {
    const prev = diagram.tables.find((t) => t.id === payload.id);
    if (prev === undefined) throw Error(`Table "${payload.id}" not found`);
    const allValueIds = diagram.tables.map((t) => t.value.id);
    const next: Edit.EditTable = {
        id: getNumberId(diagram.tables.map((t) => t.id)),
        value: {
            rows: prev.value.rows,
            schema: prev.value.schema,
            name: `${prev.value.name}_2`,
            id: getStringId(allValueIds, "table"),
        },
        position: {
            top: prev.position.top,
            left: prev.position.left + TABLE_OFFSET_WIDTH,
        },
        isRowsExpand: false,
    };

    return { ...diagram, tables: diagram.tables.concat(next) };
};
