import { Button } from "@moai/core";
import { icons } from "@moai/icon";
import React from "react";
import { HeaderChildProps } from "../../header";

interface Props extends HeaderChildProps {
    setReportVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export const HeaderReportButton = (props: Props) => (
    <Button
        onClick={() => props.setReportVisible(true)}
        size={props.size.button}
        style={props.style.button}
        icon={icons.issue}
        children="Report issue"
    />
);
