import * as Actions from "diagram/library/actions";
import { emptyDiagram } from "diagram/library/fallback";
import { EditDiagram } from "diagram/models/edit/edit";
import { ImportPayload } from "diagram/reducer/import/import";
import { showConfirm } from "@moai/core";
import { LibraryItemProps } from "../item";

export const libraryItemRemove = async (
    props: LibraryItemProps
): Promise<void> => {
    const sure = await showConfirm([
        `Are you sure you want to delete "${props.name}"?`,
        "This action is NOT reversible.",
    ]);
    if (sure === false) return;
    Actions.deleteLibraryDiagram(props.name, props.setLibrary);
    if (props.library.current !== props.name) return;
    // When delete current diagram
    const next = props.library.all.find((n) => n !== props.name);
    let source: EditDiagram;
    if (next !== undefined) {
        // If there is other diagram ---> select them
        source = Actions.openLibraryDiagram(next, props.setLibrary);
    } else {
        // If deleting the last diagram ---> create new one
        Actions.createLibraryDiagram(emptyDiagram, props.setLibrary);
        source = emptyDiagram;
    }
    const payload: ImportPayload = { source, isAppend: false };
    props.diagramDispatch({ type: "import", payload });
};
