import { Button, Input } from "@moai/core";
import * as Actions from "diagram/library/actions";
import { ImportPayload } from "diagram/reducer/import/import";
import * as React from "react";
import { LibraryPaneProps } from "../../library";
import s from "./name.module.scss";

interface Props extends LibraryPaneProps {
    name: string;
    isEdit: boolean;
    setIsEdit: React.Dispatch<React.SetStateAction<boolean>>;
}

const open = (props: Props): void => {
    const source = Actions.openLibraryDiagram(props.name, props.setLibrary);
    const payload: ImportPayload = { source, isAppend: false };
    props.diagramDispatch({ type: "import", payload });
};

const rename = async (props: Props, to: string): Promise<void> => {
    if (to === props.name) return;
    await Actions.renameLibraryDiagram(props.setLibrary, props.name, to);
};

export const LibraryItemName = (props: Props) => {
    if (props.isEdit) {
        return (
            <Input
                style={Input.style.flat}
                defaultValue={props.name}
                // Don't use onKeyDown here as the Enter will also trigger the
                // auto-focused "OK" button in the confirm dialog
                onKeyUp={async (event) => {
                    if (event.key !== "Enter") return;
                    await rename(props, event.currentTarget.value);
                    props.setIsEdit(false);
                }}
                onBlur={async (event) => {
                    await rename(props, event.currentTarget.value);
                    props.setIsEdit(false);
                }}
                autoFocus
            />
        );
    } else {
        const className = [
            s.button,
            Button.style.flat.main,
            Button.size.medium.main,
            props.library.current === props.name ? s.current : "",
        ].join(" ");
        return (
            <button
                className={className}
                onClick={() => void open(props)}
                onDoubleClick={() => void props.setIsEdit(true)}
                children={props.name}
                title="Click to select, Double-click to rename"
            />
        );
    }
};
