import * as Constants from "app/dom/constants";

/**
 * Notes about DOM structure:
 *
 * 1. Rows are all siblings of each other, but not continously (there might be
 * different elements in between). For example:
 *     <Row />
 *     <Row />
 *     <Foo /> <--- Not Row but still sibling
 *     <Row />
 *
 * 2. Columns are not siblings of each other. Some are nested (maybe together):
 *     <Column />
 *     <div> <--- Nested together
 *         <Column />
 *         <Column />
 *     </div>
 *     <div> <--- Nested alone
 *         <Column />
 *     </div>
 */

type RowDirection = "up" | "down";
type ColumnDirection = "left" | "right";
type ColumnIndex = "first" | "last" | number;

const getSibling = (
    element: Element,
    direction: RowDirection
): Element | null =>
    direction === "up"
        ? element.previousElementSibling
        : element.nextElementSibling;

/**
 * Returns the row below or above @param element, or null if element is at
 * first or last row already
 */
export const getAdjacentRow = (
    element: Element,
    direction: RowDirection
): Element | null => {
    const currRow = element.closest(Constants.TABLE_ROW_SELECTOR);
    if (currRow === null) throw Error("row is null");
    let targetRow: Element | null = getSibling(currRow, direction);
    while (targetRow !== null) {
        // Because rows are ordered continously
        if (targetRow.classList.contains(Constants.TABLE_ROW_CLASS))
            return targetRow;
        targetRow = getSibling(targetRow, direction);
    }
    return null;
};

/**
 * Returns the column at @param index of a row. Throw error if cannot found
 * such column
 */
export const getColumnByIndex = (row: Element, index: ColumnIndex): Element => {
    const all = row.querySelectorAll(Constants.TABLE_COLUMN_SELECTOR);
    const numIndex: number =
        index === "first" ? 0 : index === "last" ? all.length - 1 : index;
    const column = all[numIndex];
    if (column instanceof Element) return column;
    throw Error(".table-row doesn't have .row-control");
};

/**
 * Returns the column on left or right of @param element. Returns null if
 * element is already at the first or last column
 */
const getAdjacentColumInRow = (
    element: Element,
    direction: ColumnDirection
): Element | null => {
    const column = element.closest(Constants.TABLE_COLUMN_SELECTOR);
    if (column === null) throw Error("column is null");
    const row = column.closest(Constants.TABLE_ROW_SELECTOR);
    if (row === null) throw Error("row is null");
    const all = [...row.querySelectorAll(Constants.TABLE_COLUMN_SELECTOR)];
    const index = all.indexOf(column);
    if (direction === "left") {
        return index === 0 ? null : all[index - 1];
    } else {
        return index === all.length - 1 ? null : all[index + 1];
    }
};

/**
 * Same as @method getAdjacentColumnInRow but also reach the row below or above
 * if necessary. Only returns null if the element is also at the first or last
 * row
 */
export const getAdjacentColumn = (
    element: Element,
    direction: ColumnDirection
): Element | null => {
    const adjacentInRow = getAdjacentColumInRow(element, direction);
    if (adjacentInRow instanceof Element) return adjacentInRow;
    // Try below and above row:
    const isLeft = direction === "left";
    const adjacentRow = getAdjacentRow(element, isLeft ? "up" : "down");
    if (adjacentRow === null) return null;
    return getColumnByIndex(adjacentRow, isLeft ? "last" : "first");
};

/**
 * Returns index of element's column in element's row
 */
const getColumnIndex = (element: Element): number => {
    const row = element.closest(Constants.TABLE_ROW_SELECTOR);
    if (row === null) throw Error("Row is null");
    const column = element.closest(Constants.TABLE_COLUMN_SELECTOR);
    if (column === null) throw Error("Column is null");
    const all = row.querySelectorAll(Constants.TABLE_COLUMN_SELECTOR);
    return [...all].indexOf(column);
};

/**
 * Returns the column at the same index but in below or above row. Returns null
 * if element is already at the first or last row
 */
export const getJumpColumn = (
    element: Element,
    direction: RowDirection
): Element | null => {
    const adjacentRow = getAdjacentRow(element, direction);
    if (adjacentRow === null) return null;
    const index = getColumnIndex(element);
    return getColumnByIndex(adjacentRow, index);
};
