import * as React from "react";
import { usePopper } from "react-popper";

type PopperProps = Pick<ReturnType<typeof usePopper>, "styles" | "attributes">;

export interface CreateEdgeProps extends PopperProps {
    fromTable: number;
    fromRows: number[];
    setPopup: React.Dispatch<React.SetStateAction<HTMLElement | null>>;
    setArrow: React.Dispatch<React.SetStateAction<HTMLElement | null>>;
    setVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export type ShowCreateEdgePopup = (
    table: number,
    rows: number[],
    element: HTMLElement
) => void;

interface CreateEdgeState {
    props: CreateEdgeProps | null;
    showPopup: ShowCreateEdgePopup;
}

export const useCreateEdge = (): CreateEdgeState => {
    const [visible, setVisible] = React.useState<boolean>(false);
    // Popover state
    const [target, setTarget] = React.useState<HTMLElement | null>(null);
    const [popup, setPopup] = React.useState<HTMLElement | null>(null);
    const [arrow, setArrow] = React.useState<HTMLElement | null>(null);
    const { styles, attributes } = usePopper(target, popup, {
        placement: "right",
        modifiers: [
            { name: "arrow", options: { element: arrow } },
            { name: "hide" },
        ],
    });
    // Initial state
    const [fromTable, setFromTable] = React.useState<number>(0);
    const [fromRows, setFromRows] = React.useState<number[]>([]);

    // Memoize because we will pass this to Row and Table
    const showPopup: ShowCreateEdgePopup = React.useMemo(() => {
        return (table, rows, element) => {
            setTarget(element);
            setFromTable(table);
            setFromRows(rows);
            setVisible(true);
        };
    }, []);

    // Close on outside click
    React.useEffect(() => {
        if (visible === false) return;
        if (popup === null) return;
        const listener = (event: MouseEvent) => {
            if (!(event.target instanceof Node)) return;
            if (popup.contains(event.target)) return;
            if (target === null) throw Error("target is null");
            target.focus();
            setVisible(false);
        };
        document.addEventListener("click", listener);
        return () => document.removeEventListener("click", listener);
    }, [visible, popup, target]);

    const props = visible
        ? {
              styles,
              attributes,
              fromTable,
              fromRows,
              setArrow,
              setPopup,
              setVisible,
          }
        : null;
    return { showPopup, props };
};
