import { background } from "@moai/core";
import { CreateEdge } from "app/edge/create/create";
import { useCreateEdge } from "app/edge/create/state";
import { Header } from "app/header/header";
import { RowDragLayer } from "app/row/drag/layer/layer";
import * as Actions from "diagram/library/actions";
import { useDiagramLibrary } from "diagram/library/state";
import { EditDiagram } from "diagram/models/edit/edit";
import { useDiagram } from "diagram/state/use-diagram";
import { PrefsProps } from "pref/pref";
import React from "react";
import { usePlatform } from "utils/platform";
import { Canvas } from "./canvas/canvas";
import { useDragToScroll } from "./drag/use-drag";
import { LibraryPane } from "./library/library";
import { ViewProgress } from "./progress/progress";
import { ViewResizer } from "./resizer/resizer";
import { useCanvasCatchCopy } from "./select/canvas/copy";
import { catchDelete } from "./select/canvas/delete";
import { useCanvasCatchPaste } from "./select/canvas/paste";
import { useSelect } from "./select/select";
import s from "./view.module.scss";

export type Mode = "design" | "present" | "structure";

interface Props extends PrefsProps {}

const useDiagramFull = () => {
    const platform = usePlatform();

    const libraryState = useDiagramLibrary();
    const name = libraryState.library.current;

    // Load initial diagram
    const initial = React.useMemo((): EditDiagram => {
        return Actions.getInitialLibraryDiagram(platform, name);
    }, [platform, name]);

    const diagramState = useDiagram(initial);
    const diagram = diagramState.history.present;

    // Save current diagram
    React.useEffect(() => {
        Actions.setLibraryDiagram(diagram, name);
    }, [diagram, name]);

    return { diagram: diagramState, library: libraryState };
};

export const View = (props: Props) => {
    const { library, diagram } = useDiagramFull();
    const viewportRef = React.useRef<HTMLDivElement>(null);
    const libraryRef = React.useRef<HTMLDivElement>(null);
    const select = useSelect();
    const [isCapture, setIsCapture] = React.useState(false);
    const createEdge = useCreateEdge();

    useCanvasCatchCopy({ diagram: diagram.history.present, ids: select.ids });
    useCanvasCatchPaste({ dispatch: diagram.dispatch });

    // Drag to scroll viewport
    useDragToScroll(viewportRef);

    const header = (
        <Header
            // Diagram
            diagram={diagram.history.present}
            diagramPast={diagram.history.past}
            diagramFuture={diagram.history.future}
            diagramDispatch={diagram.dispatch}
            // Prefs
            prefs={props.prefs}
            setPrefs={props.setPrefs}
            // State
            setIsCapture={setIsCapture}
            setSelectIds={select.setIds}
            // Library
            library={library.library}
            setLibrary={library.setLibrary}
        />
    );

    const canvas = (
        <div
            className={[background.secondary, s.canvas].join(" ")}
            tabIndex={0}
            ref={viewportRef}
            onKeyDown={(event) => {
                catchDelete({
                    ids: select.ids,
                    diagramDispatch: diagram.dispatch,
                    event,
                });
            }}
        >
            <Canvas
                // Diagram
                diagram={diagram.history.present}
                diagramDispatch={diagram.dispatch}
                showCreateEdge={createEdge.showPopup}
                // View
                viewportRef={viewportRef}
                isCapture={isCapture}
                // Select
                selectedRefs={select.refs}
                selectedTables={select.tables}
                setSelectIds={select.setIds}
                // Prefs
                prefs={props.prefs}
            />
        </div>
    );

    const libraryPane = props.prefs.libraryVisible && (
        <>
            <div
                className={s.library}
                ref={libraryRef}
                style={{ width: props.prefs.libraryWidth }}
            >
                <LibraryPane
                    library={library.library}
                    setLibrary={library.setLibrary}
                    diagram={diagram.history.present}
                    diagramDispatch={diagram.dispatch}
                />
            </div>
            <ViewResizer libraryRef={libraryRef} setPrefs={props.setPrefs} />
        </>
    );

    return (
        <div className={s.container}>
            <div className={s.header} children={header} />
            <div className={s.body}>
                {libraryPane}
                {canvas}
            </div>
            <ViewProgress />
            <RowDragLayer viewportRef={viewportRef} />
            {createEdge.props && (
                <CreateEdge
                    tables={diagram.history.present.tables}
                    idKey={diagram.history.present.columns[0].value.name}
                    diagramDispatch={diagram.dispatch}
                    {...createEdge.props}
                />
            )}
        </div>
    );
};
