const BREAKING_VERSION = "6";

/**
 * Temporary safe lock to avoid compatible issues
 */
export const initVersionControl = () => {
    const prev = window.localStorage.getItem("version");
    if (prev === BREAKING_VERSION) return;
    window.localStorage.clear();
    window.localStorage.setItem("version", BREAKING_VERSION);
};

export const APP_VERSION = "15";
