import * as React from "react";
import { Platform, usePlatform } from "utils/platform";
import { getAllLibraryDiagrams } from "./actions";

/* Notes:

1. Desktop apps should not remember last opened diagram i.e. no get "current"
   from storage). Note that we still set it so error report knows
*/

export interface Library {
    current: string;
    all: string[];
}

export const CURRENT_DIAGRAM_KEY = "dg/current";
export const DEFAULT_DIAGRAM_NAME = "Untitled";

const getInitialCurrent = (platform: Platform): string => {
    if (platform !== "web") return DEFAULT_DIAGRAM_NAME; // 1
    const stored = window.localStorage.getItem(CURRENT_DIAGRAM_KEY);
    return stored ?? DEFAULT_DIAGRAM_NAME;
};

const getInitialState = (platform: Platform): Library => ({
    current: getInitialCurrent(platform),
    all: getAllLibraryDiagrams(),
});

export interface LibraryProps {
    library: Library;
    setLibrary: React.Dispatch<React.SetStateAction<Library>>;
}

const titleSuffix = "Diagram by TablePlus";

export const useDiagramLibrary = (): LibraryProps => {
    const platform = usePlatform();

    const state = React.useState<Library>(getInitialState(platform));
    const [library, setLibrary] = state;
    const { current } = library;

    // Save "current" state
    React.useEffect(() => {
        window.localStorage.setItem(CURRENT_DIAGRAM_KEY, current); // Note 1
    }, [current]);

    // Update document's title following "current"
    React.useEffect(() => {
        const prefix = current === null ? "" : `${current} | `;
        document.title = `${prefix}${titleSuffix}`;
    }, [current]);

    return { library, setLibrary };
};
