import * as React from "react";
import { initialTheme, Theme } from "./theme";

export interface Prefs {
    theme: Theme;
    zoom: number;
    footerVisible: boolean;
    edgeAnimation: boolean;
    libraryVisible: boolean;
    libraryWidth: number;
}

const defaultPrefs: Prefs = {
    theme: "auto",
    zoom: 1,
    footerVisible: true,
    libraryVisible: false,
    edgeAnimation: true,
    libraryWidth: 320,
};

const STORAGE_KEY = "dg/prefs";

const getInitial = (): Prefs => {
    const theme = initialTheme.current ?? defaultPrefs.theme;
    const stored = window.localStorage.getItem(STORAGE_KEY);
    if (stored === null) return { ...defaultPrefs, theme };
    const prefs = JSON.parse(stored) as Partial<Prefs>;
    return { ...defaultPrefs, ...prefs, theme };
};

export const usePrefs = (): PrefsProps => {
    const [prefs, setPrefs] = React.useState(getInitial);
    React.useEffect(() => {
        window.localStorage.setItem(STORAGE_KEY, JSON.stringify(prefs));
    }, [prefs]);
    return { prefs, setPrefs };
};

export interface PrefsProps {
    prefs: Prefs;
    setPrefs: React.Dispatch<React.SetStateAction<Prefs>>;
}
