import { selectOnRefClick } from "app/view/select/edge";
import { SetSelectIds } from "app/view/select/select";
import { EditDiagram, EditEdge, EditTable } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import React from "react";
import { debugLog } from "utils/debug";
import s from "./edge.module.scss";
import { useEdgeState } from "./state/state";
import { getEdgeElementId, getEdgeSubElementId } from "./utils";
import { EdgeHighlight } from "./highlight";
import { EdgesMids } from "./state/split-x";
import { RowYMap } from "./state/split-y";
import { EdgesPoints } from "./state/intersect/intersect";

interface Props {
    edge: EditEdge;
    // Tables
    fromTable: EditTable;
    toTable: EditTable;
    // Diagram
    diagramDispatch: React.Dispatch<DiagramAction>;
    // Select
    isSelected: boolean;
    setSelectIds: SetSelectIds;
    // Pref
    edgeAnimation: boolean;
    // Optimization
    edgesMids: React.MutableRefObject<EdgesMids>;
    edgesPoints: React.MutableRefObject<EdgesPoints>;
    rowYMap: React.MutableRefObject<RowYMap>;
    __hack__diagram: EditDiagram;
}

const getClassName = (props: Props): string => {
    const array = [s.container];
    if (props.isSelected) array.push(s.selected);
    return array.join(" ");
};

export const Edge = (props: Props) => {
    debugLog("Edge", props.edge.from.rows, props.edge.to.rows);

    const path = useEdgeState({
        edge: props.edge,
        fromTable: props.fromTable,
        toTable: props.toTable,
        // Optimization
        edgesMids: props.edgesMids,
        edgesPoints: props.edgesPoints,
        rowYMap: props.rowYMap,
        __hack__diagram: props.__hack__diagram,
    });
    if (path === null) return null;

    return (
        <g id={getEdgeElementId(props.edge.id)} className={getClassName(props)}>
            {path.paths.map((d, index) => (
                <path
                    key={index}
                    id={getEdgeSubElementId(props.edge.id, "path-main", index)}
                    className={s.path}
                    d={d}
                    // DO NOT define markers here. See the edge.module.scss
                />
            ))}
            {props.isSelected && props.edgeAnimation && (
                <EdgeHighlight edge={props.edge} path={path} />
            )}
            <path
                className={s.hitarea}
                d={path.paths[0]}
                onClick={selectOnRefClick({
                    refId: props.edge.id,
                    setSelectIds: props.setSelectIds,
                })}
            />
        </g>
    );
};
