/** Turn off Edit mode on blur */
export const onTableBlur = (
    isRowDragging: boolean,
    setIsEdit: React.Dispatch<React.SetStateAction<boolean>>
) => {
    if (isRowDragging) return undefined;
    return (event: React.FocusEvent<HTMLDivElement>) => {
        const target = event.relatedTarget as Node | null;
        // If inside Table
        if (event.currentTarget.contains(target)) return;
        // If inside CreateEdgePopup
        const createEdge = document.getElementById("create-edge-popup");
        if (createEdge !== null && createEdge.contains(target)) return;
        // All else -> turn off Edit mode
        setIsEdit(false);
    };
};
