import { Button, ButtonGroup, Select } from "@moai/core";
import { icons } from "@moai/icon";
import { PrefsProps } from "pref/pref";
import React from "react";
import { HeaderChildProps } from "../../header";

interface Props extends HeaderChildProps, PrefsProps {}

const zoomOptions = [25, 50, 75, 100, 125, 150, 175, 200].map((value) => {
    return { label: `${value}%`, value: value / 100 };
});

export const HeaderZoom = (props: Props) => {
    const setZoom = (zoom: number) =>
        props.setPrefs((prev) => ({ ...prev, zoom }));
    return (
        <ButtonGroup>
            {[
                <Button
                    key="out"
                    icon={icons.zoomOut}
                    onClick={() => setZoom(props.prefs.zoom - 0.25)}
                    size={props.size.button}
                    style={props.style.button}
                    title="Zoom out"
                />,
                <Select
                    key="select"
                    value={props.prefs.zoom}
                    setValue={setZoom}
                    options={zoomOptions}
                    size={props.size.select}
                    style={props.style.select}
                    title="Set zoom level"
                />,
                <Button
                    key="in"
                    icon={icons.zoomIn}
                    onClick={() => setZoom(props.prefs.zoom + 0.25)}
                    size={props.size.button}
                    style={props.style.button}
                    title="Zoom in"
                />,
            ]}
        </ButtonGroup>
    );
};
