import { background, borderColor, boxShadow } from "@moai/core";
import * as React from "react";
import { ReportForm, ReportFormProps } from "./form/form";
import s from "./report.module.css";

interface Props extends ReportFormProps {
    onClickOutside?: () => void;
}

export const ErrorReport = (props: Props) => (
    <div className={s.container}>
        <div className={[s.backdrop, background.secondary].join(" ")} />
        <div
            className={s.main}
            onClick={(event) => {
                if (event.target !== event.currentTarget) return;
                props.onClickOutside?.();
            }}
        >
            <div
                className={[
                    s.body,
                    borderColor.strong,
                    background.primary,
                    boxShadow.strong,
                ].join(" ")}
            >
                <ReportForm {...props} />
            </div>
        </div>
    </div>
);
