// html2canvas does not supported SVG explicitly so we still need
// to use this outdated library
// Ref: https://github.com/niklasvh/html2canvas/issues/2329
import { EditDiagram } from "diagram/models/edit/edit";
import domtoimage from "dom-to-image";
import { ExportImageOptions, ExportImageResult } from "../export";
// this html2canvas is a manual fork to fix a safari issue
// Ref: https://github.com/TablePlus/diagram/issues/31
// @ts-ignore
import html2canvas from "./html2canvas";

const getImageData = async (
    canvas: HTMLElement,
    options: ExportImageOptions
): Promise<string> => {
    const { format, scale } = options;
    switch (format) {
        case "png":
            const result = await html2canvas(canvas, { scale });
            return result.toDataURL();
        case "svg":
            return await domtoimage.toSvg(canvas);
    }
};

export const exportDiagramAsImage = async (
    // @TODO(https://github.com/TablePlus/diagram/issues/35)
    // Explore alternative approach which uses the diagram param
    _diagram: EditDiagram,
    options: ExportImageOptions
): Promise<ExportImageResult> => {
    const canvas = document.getElementById("canvas");
    if (canvas === null) throw Error("#canvas element is null");
    const data = await getImageData(canvas, options);
    // Remove the mimetype prefix (e.g. "data:png;base64,")
    const body = data.slice(data.indexOf(",") + 1);
    return { body };
};
