import { ButtonMenuItem } from "@moai/core";
import * as React from "react";
import { showAlert } from "@moai/core";
import { UseHostInstruction } from "utils/host";
import { Platform } from "utils/platform";

interface Props {
    platform: Platform;
}

const fn = (): void => {
    showAlert([
        "Generate from database require TablePlus app",
        <UseHostInstruction />,
    ]);
};

export const getHeaderImportSqlItems = (props: Props): ButtonMenuItem[] => {
    if (props.platform !== "web") return [];
    return [{ label: "Generate from a database…", fn }];
};
