import { Border, scrollbar } from "@moai/core";
import { View } from "app/view/view";
import { usePrefs } from "pref/pref";
import { useTheme } from "pref/theme";
import React from "react";
import { usePlatform } from "utils/platform";
import s from "./app.module.scss";
import { Footer } from "./footer/footer";
import { ErrorBoundary } from "./error/error";

export const App = () => {
    const platform = usePlatform();
    const { prefs, setPrefs } = usePrefs();
    useTheme({ prefs, setPrefs });

    return (
        <ErrorBoundary>
            <div className={[s.container, scrollbar.custom].join(" ")}>
                <div className={s.body}>
                    <View prefs={prefs} setPrefs={setPrefs} />
                </div>
                {platform === "web" && prefs.footerVisible && (
                    <>
                        <Border color="strong" />
                        <div className={s.footer}>
                            <Footer setPrefs={setPrefs} />
                        </div>
                    </>
                )}
            </div>
        </ErrorBoundary>
    );
};
