import { setJustLeftEdit } from "app/row/field/string/leave";
import { TABLE_COLUMN_SELECTOR, TABLE_ROW_SELECTOR } from "app/dom/constants";
import { focusChild } from "app/dom/dom";
import { getColumnByIndex } from "app/dom/navigate";
import { DiagramAction } from "diagram/reducer/reducer";
import { AddRowPayload } from "diagram/reducer/row/add";

interface Props {
    diagramDispatch: React.Dispatch<DiagramAction>;
    tableId: number;
}

const action = (props: Props, event: React.KeyboardEvent): void => {
    const target = event.target;
    if (!(target instanceof HTMLInputElement)) return;

    // Only proceed if tab from last column
    const column = target.closest(TABLE_COLUMN_SELECTOR);
    if (column === null) throw Error("Column is null");
    if (column.nextElementSibling !== null) return;
    // Only proceed if tab from last row
    const row = target.closest(TABLE_ROW_SELECTOR);
    if (row === null) throw Error("Row is null");
    if (row.nextElementSibling !== null) return;

    const payload: AddRowPayload = { tableId: props.tableId };
    props.diagramDispatch({ type: "add-row", payload });

    // The new row is not rendered at this moment yet so we need to schedule
    // a task to focus it later
    window.setTimeout(() => {
        const nextRow = row.nextElementSibling;
        if (nextRow === null) throw Error("nextRow is null");
        const nextColumn = getColumnByIndex(nextRow, 1);
        setJustLeftEdit(); // Show the edit mode
        focusChild(nextColumn);
    }, 0);
};

export const createRowOnTab = (props: Props) => {
    return (event: React.KeyboardEvent): void => {
        switch (event.key) {
            case "Tab":
                action(props, event);
        }
    };
};
