import { background, Button, ButtonMenu } from "@moai/core";
import { icons } from "@moai/icon";
import { exportDiagram, ExportTextOptions } from "diagram/export/export";
import { emptyDiagram } from "diagram/library/fallback";
import { EditColumn, EditDiagram, EditTable } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import { DuplicateTablePayload } from "diagram/reducer/table/duplicate";
import { RemoveTablePayload } from "diagram/reducer/table/remove";
import React from "react";
import { showAlert } from "@moai/core";
import { copyToClipboard } from "utils/clipboard";
import s from "./menu.module.css";

interface Props {
    table: EditTable;
    columns: EditColumn[];
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const remove = (props: Props): void => {
    const payload: RemoveTablePayload = { id: props.table.id };
    props.diagramDispatch({ type: "remove-table", payload });
};

const exportOptions: ExportTextOptions = {
    type: "text",
    level: "edit",
    format: "json",
};

const copy = async (props: Props): Promise<void> => {
    const diagram: EditDiagram = {
        ...emptyDiagram,
        columns: props.columns,
        tables: [props.table],
    };
    const text = exportDiagram(diagram, exportOptions).value;
    await copyToClipboard(text);
    await showAlert([
        "Your table is copied to clipboard",
        "To paste it, press Ctrl/Cmd + V",
    ]);
};

const duplicate = (props: Props): void => {
    const payload: DuplicateTablePayload = { id: props.table.id };
    props.diagramDispatch({ type: "duplicate-table", payload });
};

export const TableHeaderMenu = (props: Props) => (
    <div className={[s.container, background.primary].join(" ")}>
        <ButtonMenu
            items={[
                { label: "Copy to clipboard", fn: () => copy(props) },
                { label: "Duplicate table", fn: () => duplicate(props) },
                "divider",
                { label: "Delete table", fn: () => remove(props) },
            ]}
            button={{
                style: Button.style.flat,
                size: Button.size.small,
                icon: icons.more,
            }}
        />
    </div>
);
