import { Button, DivPx, Paragraph } from "@moai/core";
import { ErrorReport } from "app/app/error/report/report";
import React from "react";
import ReactDOM from "react-dom";

export const useHeaderReport = () => {
    const [reportVisible, setReportVisible] = React.useState(false);
    React.useEffect(() => {
        const listener = (event: KeyboardEvent) => {
            if (!event.metaKey && !event.ctrlKey) return;
            if (event.key !== "e") return;
            setReportVisible((bool) => !bool);
        };
        document.addEventListener("keydown", listener);
        return () => document.removeEventListener("keydown", listener);
    }, []);
    return { reportVisible, setReportVisible };
};

const Description = () => (
    <>
        <Paragraph>
            Thank you for helping us improve the app. We will treat your report
            as confidential and anonymous.
        </Paragraph>
        <DivPx size={8} />
        <Paragraph>
            Please describe the issue, including any steps to reproduce it:
        </Paragraph>
        <DivPx size={8} />
    </>
);

const Footer = (props: { cancel: () => void }) => (
    <Button onClick={() => props.cancel()} children="Cancel" />
);

interface Props {
    reportVisible: boolean;
    setReportVisible: React.Dispatch<React.SetStateAction<boolean>>;
}

export const HeaderReportDialog = (props: Props) => {
    if (props.reportVisible === false) return null;
    const portalContainer = document.getElementById("portals");
    if (portalContainer === null) throw Error("#portals is null");
    const closeReport = () => props.setReportVisible(false);
    return ReactDOM.createPortal(
        <ErrorReport
            type="manual"
            title="Report an Issue"
            description={<Description />}
            footer={<Footer cancel={closeReport} />}
            onDone={closeReport}
            onClickOutside={closeReport}
        />,
        portalContainer
    );
};
