/**
 * This module is to avoid lines merged vertically:
 *
 * A -----+
 *        |
 * B --+  |
 *     |  |
 *     +--+----> C
 *         ^^^^^
 *         we don't want this
 *
 * Should be:
 *
 * A -----+
 *        |
 * B --+  |
 *     |  +----> C1
 *     +--+----> C2 (C row is bigger)
 */

import { ROW_HEIGHT } from "app/dom/constants";
import { EditEdge } from "diagram/models/edit/edit";
import * as React from "react";

// Map from an edge's id to its Y position on a row
type EdgeYMap = Map<number, number>;
// Map from a row (${table}.${row}) to its EdgeYMap
export type RowYMap = Map<string, EdgeYMap>;

export const getRowYMapId = (table: number, row: number): string =>
    `${table}.${row}`;

type Ref = React.MutableRefObject<RowYMap>;
export const useRowYMap = (edges: EditEdge[]): Ref => {
    const map = React.useRef<RowYMap>(new Map());
    // Delete outdated edges references
    React.useEffect(() => {
        const edgeIds: Set<number> = new Set(edges.map((edge) => edge.id));
        [...map.current.values()].forEach((edgeYMap: EdgeYMap) => {
            [...edgeYMap.keys()].forEach((id) => {
                if (edgeIds.has(id) === false) edgeYMap.delete(id);
            });
        });
    }, [edges]);
    return map;
};

const getNextFree = (set: Set<number>, start: number): number => {
    let [i, result] = [0, start];
    while (set.has(result)) {
        result = start + (i * ROW_HEIGHT) / 2;
        i = i = i > 0 ? -i : -i + 1;
    }
    return result;
};

interface Props {
    rowYMap: RowYMap;
    rowYId: string;
    edgeId: number;
    y: number;
}

export const getRowFreeY = (props: Props): number => {
    // Ensure we have the current edgeYMap
    const { rowYId, rowYMap } = props;
    if (rowYMap.get(rowYId) === undefined) rowYMap.set(rowYId, new Map());
    const edgeYMap: EdgeYMap = rowYMap.get(rowYId)!;

    const prev = edgeYMap.get(props.edgeId);
    const next = props.y;

    // "next" is good because it's already in the record
    if (next === prev) return next;

    // "next" is new, we need to figure out a position for it. But anyway we
    // need to remove its previous record
    if (prev !== undefined) edgeYMap.delete(props.edgeId);

    // Get a "free" position as close to "next" as possible
    const used = new Set(edgeYMap.values());
    const free = getNextFree(used, next);
    edgeYMap.set(props.edgeId, free);
    return free;
};
