import { background, Border, borderColor, boxShadow } from "@moai/core";
import { EditRow, EditTable } from "diagram/models/edit/edit";
import { DiagramAction } from "diagram/reducer/reducer";
import { AddRefPayload, AddRefSide } from "diagram/reducer/ref/add";
import * as React from "react";
import * as ReactDOM from "react-dom";
import { showAlert } from "@moai/core";
import { CreateEdgeArrow } from "./arrow/arrow";
import sArrow from "./arrow/arrow.module.scss";
import s from "./create.module.css";
import { CreateEdgeFooter } from "./footer/footer";
import { CreateEdgeHeader } from "./header/header";
import { CreateEdgeSide } from "./side/side";
import { CreateEdgeProps } from "./state";

interface Props extends CreateEdgeProps {
    idKey: string; // row[idKey]
    tables: EditTable[];
    diagramDispatch: React.Dispatch<DiagramAction>;
}

const getTable = (props: Props): EditTable => {
    const id = props.fromTable;
    const table = props.tables.find((t) => t.id === id);
    if (table === undefined) throw Error(`Table "${id}" not found`);
    return table;
};

const getInitialFromTable = (props: Props): string => {
    const table = getTable(props);
    return table?.value.name ?? "";
};

const getInitialFromRows = (props: Props): string[] => {
    const ids = props.fromRows;
    const table = getTable(props);
    const rows: EditRow[] = ids.map((id) => {
        const row = table.value.rows.find((row) => row.id === id);
        if (row !== undefined) return row;
        throw Error(`Row "${id}" not found`);
    });
    return rows.map((row) => row.value[props.idKey] as string);
};

const getEdgeSide = (
    props: Props,
    tableName: string,
    rowsIds: string[]
): AddRefSide => {
    const table = props.tables.find((table) => table.value.name === tableName);
    if (table === undefined) throw Error(`Table "${tableName}" is undefined`);
    const rows: number[] = rowsIds.map((id) => {
        const row = table.value.rows.find((row) => {
            return row.value[props.idKey] === id;
        });
        if (row !== undefined) return row.id;
        throw Error(`Row "${id}" not found`);
    });
    return { table: table.id, rows };
};

const submit = (
    props: Props,
    from: [string, string[]],
    to: [string, string[]]
): void => {
    try {
        const payload: AddRefPayload = {
            from: getEdgeSide(props, from[0], from[1]),
            to: getEdgeSide(props, to[0], to[1]),
        };
        props.diagramDispatch({ type: "add-ref", payload });
    } catch (error) {
        showAlert(["Cannot create ref", error.message]);
    }
};

const portalContainer = document.getElementById("portals");
if (portalContainer === null) throw Error("#portals is null");

const className = [
    s.container,
    sArrow.menu,
    background.primary,
    boxShadow.strong,
    borderColor.strong,
].join(" ");

export const CreateEdge = (props: Props) => {
    const [fromTable, setFromTable] = React.useState<string>(() => {
        return getInitialFromTable(props);
    });
    const [fromRows, setFromRows] = React.useState<string[]>(() => {
        return getInitialFromRows(props);
    });
    const [toTable, setToTable] = React.useState<string>("");
    const [toRows, setToRows] = React.useState<string[]>([""]);

    const popup = (
        <div
            id="create-edge-popup"
            className={className}
            ref={props.setPopup}
            style={props.styles.popper}
            {...props.attributes.popper}
        >
            <form
                className={s.form}
                onSubmit={(event) => {
                    event.preventDefault();
                    submit(props, [fromTable, fromRows], [toTable, toRows]);
                    props.setVisible(false);
                }}
            >
                <CreateEdgeHeader />
                <Border color="weak" />
                <CreateEdgeSide
                    label="From"
                    idKey={props.idKey}
                    tables={props.tables}
                    table={fromTable}
                    setTable={setFromTable}
                    rows={fromRows}
                    setRows={setFromRows}
                    autoFocus={false}
                />
                <Border color="weak" />
                <CreateEdgeSide
                    label="To"
                    idKey={props.idKey}
                    tables={props.tables}
                    table={toTable}
                    setTable={setToTable}
                    rows={toRows}
                    setRows={setToRows}
                    autoFocus={true}
                />
                <Border color="weak" />
                <CreateEdgeFooter />
            </form>
            <CreateEdgeArrow
                setArrow={props.setArrow}
                style={props.styles.arrow}
                attributes={props.attributes.arrow}
            />
        </div>
    );

    return ReactDOM.createPortal(popup, portalContainer);
};
