import { Border } from "@moai/core";
import * as React from "react";
import { Platform, usePlatform } from "utils/platform";
import { HeaderPrimary, HeaderPrimaryProps } from "./primary/primary";
import { HeaderReportDialog, useHeaderReport } from "./report/report";
import { HeaderSecondary, HeaderSecondaryProps } from "./secondary/secondary";
import sBase from "./style/base.module.scss";
import sMac from "./style/mac.module.scss";
import sWin from "./style/win.module.scss";
import { getHeaderSize, HeaderSize } from "./utils/size";
import { getHeaderStyle, HeaderStyle } from "./utils/style";

type PrimaryProps = Omit<
    HeaderPrimaryProps,
    "isHeaderExpand" | "setIsHeaderExpand" | "size"
>;

export interface HeaderChildProps {
    size: HeaderSize;
    style: HeaderStyle;
}

type SecondaryProps = Omit<HeaderSecondaryProps, "">;

type Props = Omit<
    PrimaryProps & SecondaryProps,
    "style" | "size" | "setReportVisible"
>;

const getPlatformClasses = (platform: Platform) => {
    const s = platform === "mac" ? sMac : platform === "win" ? sWin : null;
    return {
        container: [sBase.container, s?.container ?? ""].join(" "),
        borderStrong: [sBase.borderStrong, s?.borderStrong ?? ""].join(" "),
        borderWeak: [sBase.borderWeak, s?.borderWeak ?? ""].join(" "),
    };
};

const HeaderPure = (props: Props) => {
    const [isExpand, setIsExpand] = React.useState(false);
    const platform = usePlatform();
    const size = getHeaderSize(platform);
    const style = getHeaderStyle(platform);
    const { reportVisible, setReportVisible } = useHeaderReport();
    const classes = getPlatformClasses(platform);
    return (
        <div className={classes.container}>
            <HeaderPrimary
                {...props}
                isHeaderExpand={isExpand}
                setIsHeaderExpand={setIsExpand}
                size={size}
                style={style}
            />
            {isExpand && (
                <>
                    <div className={classes.borderWeak}>
                        <Border color="weak" />
                    </div>
                    <HeaderSecondary
                        {...props}
                        setReportVisible={setReportVisible}
                        size={size}
                        style={style}
                    />
                </>
            )}
            <HeaderReportDialog
                reportVisible={reportVisible}
                setReportVisible={setReportVisible}
            />
            <div className={classes.borderStrong}>
                <Border color="strong" />
            </div>
        </div>
    );
};

export const Header = React.memo(HeaderPure);
