import { Button, ButtonMenu, ButtonMenuItem } from "@moai/core";
import { icons } from "@moai/icon";
import { duplicateLibraryDiagram } from "diagram/library/actions";
import * as React from "react";
import { usePlatform } from "utils/platform";
import { LibraryItemProps } from "../item";
import { libraryItemExport } from "./export";
import { libraryItemRemove } from "./remove";

const duplicate = (props: LibraryItemProps): void => {
    duplicateLibraryDiagram(props.name, props.library.all, props.setLibrary);
};

export const LibraryItemMenu = (props: LibraryItemProps) => {
    const platform = usePlatform();
    const exportItem = libraryItemExport.bind(null, props.name, platform);
    const items: ButtonMenuItem[] = [
        { label: "Save to file", fn: () => exportItem("file") },
        { label: "Copy to clipboard", fn: () => exportItem("clipboard") },
        { label: "Duplicate diagram", fn: () => duplicate(props) },
        "divider",
        { label: "Delete diagram", fn: () => libraryItemRemove(props) },
    ];
    return (
        <ButtonMenu
            button={{
                icon: icons.more,
                style: Button.style.flat,
                title: "More options",
            }}
            items={items}
        />
    );
};
