import React from "react";
import { addExternalFnDirect } from "utils/external";
import { PrefsProps } from "./pref";

export type Theme = "light" | "dark" | "auto";

const themes: Theme[] = ["light", "dark", "auto"];

const isTheme = (theme: any): theme is Theme => themes.includes(theme);

const applyTheme = (theme: Theme): void => {
    const themeClasses = themes.map((name) => `theme-${name}`);
    document.documentElement.classList.remove(...themeClasses);
    document.documentElement.classList.add(`theme-${theme}`);
};

const getTheme = (theme: string): Theme => {
    if (isTheme(theme)) return theme;
    throw Error(`Theme is unknown: ${theme}`);
};

export const initialTheme: {
    current: null | Theme;
} = {
    current: null,
};

addExternalFnDirect("setTheme", (text: string) => {
    const theme = getTheme(text);
    applyTheme(theme);
    initialTheme.current = theme;
});

export const useTheme = (props: PrefsProps) => {
    const { theme } = props.prefs;
    const { setPrefs } = props;

    React.useEffect(() => {
        applyTheme(theme);
    }, [theme]);

    React.useEffect(() => {
        addExternalFnDirect("setTheme", (text: string): void => {
            setPrefs((prev) => ({ ...prev, theme: getTheme(text) }));
        });
    }, [setPrefs]);
};
