import { DiagramReducer } from "../reducer";
import { importDiagram } from "diagram/import/import";
import { appendDiagram } from "./append";

export interface ImportPayload {
    isAppend: boolean;
    source: unknown;
}

export type DiagramImportActions = { type: "import"; payload: ImportPayload };

export const diagramImportReducer: DiagramReducer = (prev, action) => {
    switch (action.type) {
        case "import":
            const next = importDiagram(action.payload.source);
            if (action.payload.isAppend === false) return next;
            return appendDiagram(prev, next);
        default:
            return prev;
    }
};
