import * as React from "react";
import { HeaderChildProps } from "../../header";
import { PrefsProps, Prefs } from "pref/pref";
import { Button } from "@moai/core";
import { icons } from "@moai/icon";

interface Props extends HeaderChildProps, PrefsProps {}

const toggle = (prefs: Prefs): Prefs => ({
    ...prefs,
    libraryVisible: !prefs.libraryVisible,
});

export const HeaderLibrary = (props: Props) => (
    <Button
        onClick={() => void props.setPrefs(toggle)}
        selected={props.prefs.libraryVisible}
        size={props.size.button}
        style={props.style.button}
        icon={icons.menuOpen}
    />
);
