import { EditDiagram } from "diagram/models/edit/edit";
import { makeBaseDiagramFromEdit } from "../base";
import { ExportSqlOptions, ExportSqlResult } from "../export";

export const exportSqlDrivers = [
    "MySQL",
    "MariaDB",
    "SQLite",
    "PostgreSQL",
    "Cassandra",
    "Snowflake",
    "MicrosoftSQLServer",
];

export const exportDiagramAsSql = (
    diagram: EditDiagram,
    options: ExportSqlOptions
): ExportSqlResult => {
    // At the moment all drivers use the same export format so we only check
    // for existence here
    if (exportSqlDrivers.includes(options.driver) === false) {
        throw Error(`Unknown driver: ${options.driver}`);
    }
    const base = makeBaseDiagramFromEdit(diagram);
    return { base };
};
