import { Switcher, SwitcherOption } from "@moai/core";
import { icons } from "@moai/icon";
import { PrefsProps } from "pref/pref";
import { Theme } from "pref/theme";
import React from "react";
import { HeaderChildProps } from "../../header";

const options: SwitcherOption<Theme>[] = [
    { value: "light", label: "Light", icon: icons.flash, key: "light" },
    { value: "auto", label: "System", key: "auto" },
    { value: "dark", label: "Dark", icon: icons.moon, key: "dark" },
];

interface Props extends HeaderChildProps, PrefsProps {}

export const HeaderTheme = (props: Props) => (
    <Switcher<Theme>
        value={props.prefs.theme}
        setValue={(theme) => props.setPrefs((prev) => ({ ...prev, theme }))}
        options={options}
        size={props.size.button}
        style={props.style.button}
    />
);
