import { ButtonMenuItem } from "@moai/core";
import { ImportPayload } from "diagram/reducer/import/import";
import { DiagramAction } from "diagram/reducer/reducer";
import { showPrompt } from "@moai/core";

const dispatch = (props: Props, source: unknown) => {
    const payload: ImportPayload = { source, isAppend: false };
    props.diagramDispatch({ type: "import", payload });
};

const importText = async (props: Props): Promise<void> => {
    const source = await showPrompt([
        "Paste a diagram into the textbox below",
        'To export diagrams as text, go to "Export" > "Copy to clipboard"',
    ]);
    if (source === null || !source) return;
    dispatch(props, source);
};

const importFile = (props: Props) => {
    const input = document.createElement("input");
    input.type = "file";
    input.addEventListener("change", (event) => {
        const file = (event as any).target.files[0];
        const reader = new FileReader();
        reader.readAsText(file);
        reader.addEventListener("load", (event) => {
            const source = event.target?.result;
            if (typeof source !== "string")
                throw Error(`Unknown content type: ${typeof source}`);
            dispatch(props, source);
        });
    });
    input.click();
};

interface Props {
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const getHeaderImportTextItems = (props: Props): ButtonMenuItem[] => [
    { label: `Open file…`, fn: () => importFile(props) },
    { label: `Import from text…`, fn: () => importText(props) },
];
