export const getTableElementId = (tableId: number) => `table . ${tableId}`;

export const getTableElement = (tableId: number): HTMLDivElement => {
    const element = document.getElementById(getTableElementId(tableId));
    if (element instanceof HTMLDivElement) return element;
    throw Error("Element is not HTMLDivElement");
};

export const getRowElementId = (tableId: number, rowId: number) =>
    `row . ${tableId} . ${rowId}`;

type RowElement = HTMLDivElement;

export const getRowElement = (
    tableId: number,
    rowId: number
): RowElement | null => {
    const element = document.getElementById(getRowElementId(tableId, rowId));
    if (element instanceof HTMLDivElement) return element;
    return null;
};

export const focusChild = (column: Element): void => {
    const element = column.querySelector("select,input,button,[tabindex]");
    if (element instanceof HTMLElement) return element.focus();
    throw Error("No focusable element under column");
};
