import { exportDiagram, ExportTextOptions } from "diagram/export/export";
import { importDiagram } from "diagram/import/import";
import { EditDiagram } from "diagram/models/edit/edit";
import { getStringId } from "diagram/reducer/id";
import { showConfirm } from "@moai/core";
import { Platform } from "utils/platform";
import { emptyDiagram, fallbackDiagram } from "./fallback";
import { DEFAULT_DIAGRAM_NAME, Library } from "./state";

type Dispatch = React.Dispatch<React.SetStateAction<Library>>;

const DOC_PREFIX = "dg/doc/";

const getStorageKey = (id: string): string => `${DOC_PREFIX}${id}`;

// Get

export const getLibraryDiagram = (name: string): EditDiagram | null => {
    const key = getStorageKey(name);
    const stored = window.localStorage.getItem(key);
    if (stored === null) return null;
    return importDiagram(stored);
};

// Set

export const setLibraryDiagram = (diagram: EditDiagram, id: string): void => {
    type Options = ExportTextOptions;
    const options: Options = { type: "text", level: "edit", format: "json" };
    const value = exportDiagram(diagram, options);
    const key = getStorageKey(id);
    window.localStorage.setItem(key, value.value);
};

// Create

const createInStorage = (diagram: EditDiagram): string => {
    const names = getAllLibraryDiagrams();
    const name = getStringId(names, "Untitled");
    setLibraryDiagram(diagram, name);
    return name;
};

const createUpdater = (name: string) => (prev: Library): Library => ({
    current: name,
    all: [name, ...prev.all],
});

export const createLibraryDiagram = (
    diagram: EditDiagram,
    dispatch: Dispatch
): void => {
    const name = createInStorage(diagram);
    dispatch(createUpdater(name));
};

// Duplicate

export const duplicateLibraryDiagram = (
    from: string,
    prevAll: string[],
    dispatch: Dispatch
): void => {
    const diagram = getLibraryDiagram(from);
    if (diagram === null) throw Error(`Diagram "${from}" is null`);
    const to = getStringId(prevAll, from);
    const fromIndex = prevAll.indexOf(from);
    if (fromIndex === -1) throw Error(`Diagram "${from}" not found`);
    const nextAll = [...prevAll];
    nextAll.splice(fromIndex + 1, 0, to);
    setLibraryDiagram(diagram, to);
    dispatch((prev) => ({ ...prev, all: nextAll }));
};

// Delete

const deleteUpdater = (name: string) => (prev: Library): Library => {
    const all = prev.all.filter((n) => n !== name);
    return { ...prev, all };
};

const deleteInStorage = (name: string): void => {
    window.localStorage.removeItem(getStorageKey(name));
};

export const deleteLibraryDiagram = (
    name: string,
    dispatch: Dispatch
): void => {
    deleteInStorage(name);
    dispatch(deleteUpdater(name));
};

// Rename

const renameInStorage = async (
    fromName: string,
    toName: string
): Promise<boolean> => {
    const toValue = getLibraryDiagram(toName);
    if (toValue !== null) {
        const sure = await showConfirm(`Do you want to override "${toName}"?`);
        if (sure === false) return false;
    }
    const fromValue = getLibraryDiagram(fromName);
    if (fromValue === null) throw Error(`No diagram at "${fromName}"`);
    setLibraryDiagram(fromValue, toName);
    deleteInStorage(fromName);
    return true;
};

const renameUpdater = (from: string, to: string) => {
    return (prev: Library): Library => {
        const current = prev.current === from ? to : prev.current;
        const index = prev.all.indexOf(from);
        if (index === -1) throw Error(`"${from}" not found`);
        let all: string[];
        if (prev.all.includes(to)) {
            all = prev.all.filter((name) => name !== from);
        } else {
            all = [...prev.all];
            all[index] = to;
        }
        return { current, all };
    };
};

export const renameLibraryDiagram = async (
    dispatch: Dispatch,
    fromName: string,
    toName: string
): Promise<void> => {
    const renamed = await renameInStorage(fromName, toName);
    if (renamed) dispatch(renameUpdater(fromName, toName));
};

// List

export const getAllLibraryDiagrams = (): string[] => {
    const list = Object.keys(window.localStorage)
        .filter((key) => key.startsWith(DOC_PREFIX))
        .map((key) => key.replace(DOC_PREFIX, ""));
    if (list.length === 0) list.push(DEFAULT_DIAGRAM_NAME);
    return list;
};

// Initial

export const getInitialLibraryDiagram = (
    platform: Platform,
    id: string
): EditDiagram => {
    // Desktop apps should not have initial diagram
    if (platform !== "web") return emptyDiagram;
    const stored = getLibraryDiagram(id);
    if (stored !== null) return stored;
    // Previous version (pre-Library era)
    const legacy = window.localStorage.getItem("diagram");
    if (legacy !== null) return importDiagram(legacy);
    // Default
    return fallbackDiagram;
};

// Open

const openUpdater = (name: string) => (prev: Library): Library => ({
    ...prev,
    current: name,
});

export const openLibraryDiagram = (
    name: string,
    dispatch: Dispatch
): EditDiagram => {
    const diagram = getLibraryDiagram(name);
    if (diagram === null) throw Error(`Diagram not found "${name}"`);
    dispatch(openUpdater(name));
    return diagram;
};
