import { EditTable } from "diagram/models/edit/edit";
import { Position } from "diagram/models/layout";
import { DiagramAction } from "diagram/reducer/reducer";
import { DraggableEventHandler } from "react-draggable";

const round = (num: number) => Math.round(num / 16) * 16;

interface Props {
    table: EditTable;
    diagramDispatch: React.Dispatch<DiagramAction>;
}

export const onTableDragStop = (props: Props): DraggableEventHandler => {
    return (_event, data) => {
        if (data.x === 0 && data.y === 0) return;
        const position: Position = {
            left: round(props.table.position.left + data.x),
            top: round(props.table.position.top + data.y),
        };
        props.diagramDispatch({
            type: "set-table-position",
            payload: { id: props.table.id, position },
        });
    };
};
