import { Button } from "@moai/core";
import { icons } from "@moai/icon";
import { Prefs } from "pref/pref";
import * as React from "react";

interface Props {
    setPrefs: React.Dispatch<React.SetStateAction<Prefs>>;
}

const closeFooter = (prev: Prefs): Prefs => ({ ...prev, footerVisible: false });

export const FooterClose = (props: Props) => (
    <Button
        style={Button.style.flat}
        onClick={() => props.setPrefs(closeFooter)}
        icon={icons.chevronDown}
        title="Close the footer"
    />
);
