import { ButtonMenuItem } from "@moai/core";
import { ImportPayload } from "diagram/reducer/import/import";
import { DiagramAction } from "diagram/reducer/reducer";

type setIsBusy = React.Dispatch<React.SetStateAction<boolean>>;

const loadRemote = async (props: Props, url: string) => {
    props.setIsBusy(true);
    const source = await fetch(url).then((response) => response.json());
    const payload: ImportPayload = { source, isAppend: false };
    props.diagramDispatch({ type: "import", payload });
    props.setIsBusy(false);
};

interface Props {
    diagramDispatch: React.Dispatch<DiagramAction>;
    setIsBusy: setIsBusy;
}

export const getHeaderImportSampleItems = (props: Props): ButtonMenuItem[] =>
    [
        ["Load the Airlines sample", "airlines"],
        ["Load the IMDb sample", "imdb"],
        ["Load the Employees sample", "employees"],
        // "divider",
        // ["Import a small sample (3 tables)", "1"],
        // ["Import sample 2 (Table with many columns)", "2"],
        // ["Import sample 3 (28 tables)", "3"],
        // ["Import a large sample (1710 tables)", "4"],
    ].map((item) => {
        // if (item === "divider") return "divider";
        const [label, name] = item;
        const url = `https://diagramplus.com/samples/${name}.json`;
        return { label, fn: () => loadRemote(props, url) };
    });
