import { background, borderColor } from "@moai/core";
import * as React from "react";
import s from "./arrow.module.scss";

interface Props {
	style: React.CSSProperties;
	setArrow: React.Dispatch<React.SetStateAction<HTMLElement | null>>;
	attributes: { [key: string]: string };
}

export const CreateEdgeArrow = (props: Props) => (
	<div
		style={props.style}
		className={s.container}
		ref={props.setArrow}
		{...props.attributes}
	>
		<div
			className={[
				s.shape,
				borderColor.strong,
				background.primary,
			].join(" ")}
		/>
	</div>
);
