import { BaseDiagram } from "diagram/models/base/base";
import { EditDiagram } from "diagram/models/edit/edit";
import { exportDiagramAsImage } from "./image/image";
import { exportDiagramAsSql } from "./sql/sql";
import { exportDiagramAsText } from "./text/text";

export interface ExportImageOptions {
    type: "image";
    format: "png" | "svg";
    scale: 1 | 2;
}

export interface ExportImageResult {
    body: string;
}

export interface ExportSqlOptions {
    type: "sql";
    driver: string;
}

export interface ExportSqlResult {
    base: BaseDiagram;
}

export type ExportTextFormat = "json" | "base64"

export interface ExportTextOptions {
    type: "text";
    level: "base" | "edit";
    format: ExportTextFormat;
}

export interface ExportTextResult {
    value: string;
}

export type ExportOptions =
    | ExportImageOptions
    | ExportSqlOptions
    | ExportTextOptions;

export type ExportResult<T> = T extends ExportTextOptions
    ? ExportTextResult
    : T extends ExportSqlOptions
    ? ExportSqlResult
    : T extends ExportImageOptions
    ? Promise<ExportImageResult>
    : never;

export const exportDiagram = <T extends ExportOptions>(
    diagram: EditDiagram,
    options: T
): ExportResult<T> => {
    // To explain the "any" in this function:
    // https://github.com/microsoft/TypeScript/issues/24929
    switch (options.type) {
        case "text":
            return exportDiagramAsText(diagram, options as any) as any;
        case "image":
            return exportDiagramAsImage(diagram, options as any) as any;
        case "sql":
            return exportDiagramAsSql(diagram, options as any) as any;
    }
};
