import { Platform } from "../platform";
import { saveFileInWeb } from "./web";
import { saveFileInApp } from "./app";

export type FileFormat = "png" | "svg" | "diagram";

export const fileExtensions: {
    [key in FileFormat]: string;
} = {
    png: "png",
    svg: "svg",
    diagram: "diagram",
};

export interface SaveFileProps {
    platform: Platform;
    format: FileFormat;
    content: string;
    name: string;
}

export const saveFile = (props: SaveFileProps): void => {
    switch (props.platform) {
        case "web":
            return saveFileInWeb(props);
        case "mac":
        case "win":
            return saveFileInApp(props);
    }
};
