import { Checkbox } from "@moai/core";
import { PrefsProps } from "pref/pref";
import * as React from "react";

interface Props extends PrefsProps {}

export const HeaderAnimation = (props: Props) => (
    <Checkbox
        value={props.prefs.edgeAnimation}
        setValue={(value) =>
            props.setPrefs((prev) => ({ ...prev, edgeAnimation: value }))
        }
        children="Show animation on highlighted arrows"
    />
);
