import { EditTable } from "diagram/models/edit/edit";
import { getTableElement } from "./dom";
import * as Constants from "./constants";

interface Size {
    width: number;
    height: number;
}

export const getTableEstimatedSize = (table: EditTable): Size => {
    // We hide rows when there are more
    const count = table.isRowsExpand
        ? table.value.rows.length
        : Math.min(table.value.rows.length, Constants.MAX_ROWS_COUNT + 1);
    // Use an estimated value based on the number of rows (these values
    // come from manually measuring the initial render)
    return { width: Constants.TABLE_CLIENT_WIDTH, height: count * 24 + 64 };
};

export const getTableSize = (table: EditTable): Size => {
    // Try to get exact size if possible
    try {
        const element = getTableElement(table.id);
        // Double check to avoid using one from previous diagram. This happens
        // when import a new layout
        if (element.dataset.name !== table.value.name)
            throw Error("Outdated table from previous diagram");
        return { width: element.clientWidth, height: element.clientHeight };
    } catch (e) {
        // If can't find the element then use estimated size
        // (This happens when the algo is run before rendering, like in import)
        return getTableEstimatedSize(table);
    }
};

export const getRowHeight = (edgesToHereCount: number): number => {
    const additionalHeight =
        (edgesToHereCount - 1) * (Constants.ROW_HEIGHT / 2);
    return Constants.ROW_HEIGHT + Math.max(additionalHeight, 0);
};
