import { SaveFileProps, fileExtensions, FileFormat } from "./file";
import { saveFileToHost } from "utils/host";
import { encodeText } from "utils/encode";

const clean = (text: string): string => {
    let result = text;
    // TODO: Investigate more why the encoder can't encode these characters
    // instead of manually maintaining this blacklist
    ["&%23xA;", "&%239;"].forEach((word) => {
        result = result.split(word).join("");
    });
    return result;
};

const isBase64Format = (format: FileFormat): boolean => {
    switch (format) {
        case "diagram":
        case "svg":
            return false;
        case "png":
            return true;
    }
};

const toBase64 = (props: SaveFileProps): string => {
    // If base64 already
    if (isBase64Format(props.format)) return props.content;
    // If not ---> convert text to base64
    return encodeText(clean(props.content));
};

export const saveFileInApp = (props: SaveFileProps): void => {
    if (props.platform === "web")
        throw Error('Calling saveFileInApp with "web" platform');
    saveFileToHost(props.platform, {
        data: toBase64(props),
        ext: fileExtensions[props.format],
        name: props.name,
        title: `Save ${props.name} as`,
    });
};
