export const ROW_HEIGHT = 24;
export const MAX_ROWS_COUNT = 50;
export const GRID_CELL = 16;
export const TABLE_CLIENT_WIDTH = GRID_CELL * 16;
// Basically width of 2 columns
export const TABLE_BODY_WIDTH = TABLE_CLIENT_WIDTH - 16;
// When expand (edit mode) Table will take 2xGRID_CELL each side
export const TABLE_MARGIN_SINGLE = GRID_CELL * 2 + GRID_CELL;
export const TABLE_MARGIN_DOUBLE = TABLE_MARGIN_SINGLE * 2;
export const TABLE_OFFSET_WIDTH = TABLE_CLIENT_WIDTH + TABLE_MARGIN_DOUBLE;

export const TABLE_ROW_CLASS = "table-row";
export const TABLE_ROW_SELECTOR = `.${TABLE_ROW_CLASS}`;
export const TABLE_COLUMN_CLASS = "row-control";
export const TABLE_COLUMN_SELECTOR = `.${TABLE_COLUMN_CLASS}`;
