import { exportDiagram } from "diagram/export/export";
import { EditDiagram } from "diagram/models/edit/edit";
import { ImportPayload } from "diagram/reducer/import/import";
import { DiagramAction } from "diagram/reducer/reducer";
import React from "react";
import { addExternalFn, addExternalFnDirect } from "utils/external";

const registerJson = addExternalFn("importDiagramJSON");
const registerObject = addExternalFn("importDiagramObject");
const registerBase64 = addExternalFn("importDiagramBase64");

export const useDiagramExternal = (
    diagram: EditDiagram,
    diagramDispatch: React.Dispatch<DiagramAction>
) => {
    React.useEffect(() => {
        // Having 3 separated methods is only for backware-compatibility. The
        // importDiagram can handle any format
        const importDiagram = (source: unknown): void => {
            const payload: ImportPayload = { source, isAppend: false };
            diagramDispatch({ type: "import", payload });
        };
        registerJson(importDiagram);
        registerObject(importDiagram);
        registerBase64(importDiagram);
    }, [diagramDispatch]);

    React.useEffect(() => {
        const bound = exportDiagram.bind(null, diagram);
        addExternalFnDirect("exportDiagramBase", () => {
            return bound({ type: "text", level: "base", format: "json" });
        });
        addExternalFnDirect("exportDiagramEdit", () => {
            return bound({ type: "text", level: "edit", format: "json" });
        });
    }, [diagram]);
};
